import React from "react";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import img from "../../../../../assets/img/confirm-img.png";

const ConfirmChangesModal = ({ show, setShow, onConfirm }) => {
  const handleClose = () => setShow(false);

  return (
    <Modal show={show} onHide={handleClose} className="custom-modal" centered>
      <Modal.Header closeButton>
        <Modal.Title>Confirmation</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div className="inactive-account-modal-wrapper text-center">
          <img src={img} alt="" />
          <h4 className="fw-bold">Are you sure want to confirm ?</h4>
          <p className="description mb-3 text-center">
            Confirm changes 
          </p>
        </div>
      </Modal.Body>
      <Modal.Footer className="gap-2">
        <Button
          variant="primary"
          onClick={() => {
            onConfirm();
            setShow(false);
          }}
        >
          Submit
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default ConfirmChangesModal;
