import React from "react";
import "./customRangeSlider.scss";
import { Slider } from "antd";

const CustomSliderComponent = ({ change, ...props }) => {
  return (
    <Slider
      {...props}
      range={{
        draggableTrack: true,
      }}
      defaultValue={[props.min || 0, props.max || 100]}
      onChange={(e) => {
        change({ value: e });
      }}
      marks={{ 0: "0", [props.max]: props.max }}
    />
  );
};

export default CustomSliderComponent;
