import { useNavigate, useParams } from "react-router-dom";
import "./VerticalStepper.scss";
import { LeftArrowIcon } from "../../../../../../../assets/img/svg";
import { Location, Package } from "../../../../../../../assets/SvgIcons";
import { useContext } from "react";
import { VenueDetailsContext } from "../VenueDetailsContext";

const VerticalStepper = ({ stepperList = [], stepperState }) => {
  const param = useParams();
  const { activeStepper, setActiveStepper } = useContext(VenueDetailsContext);
  const navigate = useNavigate();

  return (
    <div className="VerticalStepper h-100">
      <div className="mb-4 d-flex align-items-center">
        <span
          role="button"
          onClick={() => navigate("/settings/account-details/venue-details")}
        >
          <LeftArrowIcon />
          <label role="button" className="ms-2">
            Venue Listing
          </label>
        </span>
      </div>
      <div className="progress-stepper-items">
        {stepperList.map((data, index) => (
          <>
            <div
              role={param.venueId ? "button" : "none"}
              onClick={() => {
                param.venueId && setActiveStepper(index);
              }}
              className={`progress-stepper-item ${
                activeStepper === index
                  ? "active"
                  : (stepperState.isOverViewAvailable &&
                      data.title === "Overview") ||
                    (stepperState.isTimeDetailAvailable &&
                      data.title === "Time Details") ||
                    (stepperState.isFBItemsAvailable &&
                      data.title === "F&B Items") ||
                    (stepperState.isFBPackageAvailable &&
                      data.title === "F&B Packages") ||
                    (stepperState.isFBMenuAvailable &&
                      data.title === "F&B Menu") ||
                    (stepperState.isAmenitiesAvailable &&
                      data.title === "Amenities") ||
                    (stepperState.isVenueRentalAvailable &&
                      data.title === "Venue Rentals")
                  ? "done"
                  : ""
              }`}
            >
              <div>
                <div className="icon-container">
                  {activeStepper === index ? (
                    <Package />
                  ) : (stepperState.isOverViewAvailable &&
                      data.title === "Overview") ||
                    (stepperState.isTimeDetailAvailable &&
                      data.title === "Time Details") ||
                    (stepperState.isFBItemsAvailable &&
                      data.title === "F&B Items") ||
                    (stepperState.isFBPackageAvailable &&
                      data.title === "F&B Packages") ||
                    (stepperState.isFBMenuAvailable &&
                      data.title === "F&B Menu") ||
                    (stepperState.isAmenitiesAvailable &&
                      data.title === "Amenities") ||
                    (stepperState.isVenueRentalAvailable &&
                      data.title === "Venue Rentals") ? (
                    <Location />
                  ) : (
                    <Package />
                  )}
                </div>
              </div>
              <span className="ms-2 icon-title">{data.title}</span>
            </div>
            <div className="progress-stepper-line my-1" />
          </>
        ))}
      </div>
    </div>
  );
};

export default VerticalStepper;
