import React, { useContext, useState } from "react";
import {
  CalenderIconGrey,
  CancelSquareIcon,
  DeleteAction,
  EditAction,
  EditPenIconSquare,
  ThreeDotAction,
  TickIconCircle,
  ViewAction,
} from "../../../../../assets/img/svg";
import datedesign from "../.././../../../assets/img/calendar-time.png";
import userdesign from "../../../../../assets/img/user.png";
import { Dropdown } from "react-bootstrap";
import usePermission from "../../../../common/hooks/userPermission";
import { CalendarAddReminder } from "../../CalendarAddReminder";
import { Spinner } from "@syncfusion/ej2-react-popups";
import remindersServices from "../../../../../services/api-services/reminderServices";
import apiStatus from "../../../../../utils/apiResponseHandler";
import { enumList } from "../../../../../utils/enumList";
import { formatDate } from "../../../../../utils/JSutils";
import CalenderContext from "../../CalenderContext";
import CancelReminderModal from "../../popups/CancelReminderModal";
import MarkCompleteReminderModal from "../../popups/MarkCompleteReminderModal";

const CalendarReminders = ({ remainders }) => {
  const [statusLoading, setStatusLoading] = useState(false);
  const [reminderId, setReminderId] = useState(null);
  const [showAddReminderCanvas, setShowAddReminder] = useState(false);
  const [editData, setEditData] = useState(null);
  const { getMonthInquiryDetails } = useContext(CalenderContext);
  const [showConfirmModal, setShowConfirmModal] = useState(false);
  const [showMarkCompleteModal, setShowMarkCompleteModal] = useState(false);
  const [actionData, setActionData] = useState({
    action: "",
    reminderId: "",
    statusId: "",
  });

  const editReminder = (data) => {
    setEditData(data);
    setShowAddReminder(true);
  };

  const handleStatus = (id, action) => {
    let statusId = "";
    if (action === "complete") {
      statusId = enumList.statusEnum.value.Completed;
      setActionData({
        action: action,
        reminderId: id,
        statusId: statusId,
      });
      setShowMarkCompleteModal(true);
    } else if (action === "cancel") {
      statusId = enumList.statusEnum.value.Cancelled;
      setActionData({
        action: action,
        reminderId: id,
        statusId: statusId,
      });
      setShowConfirmModal(true);
    }
  };

  const changeReminderStatus = async (e) => {
    e.preventDefault();
    setShowConfirmModal(false);
    setShowMarkCompleteModal(false);
    try {
      setStatusLoading(true);
      const { title } = await remindersServices.updateReminderStatus(
        actionData.reminderId,
        actionData.statusId
      );
      if (title === apiStatus.success) {
        getMonthInquiryDetails();
      }
    } catch (error) {
      console.log(error);
    } finally {
      setStatusLoading(false);
    }
  };

  const reminderList = remainders?.map((item) => {
    const newItem = { ...item };
    const [day, month, year] = newItem?.selectDate?.split("-");
    const formattedDate = `${year}-${month}-${day}T00:00:00.000Z`;
    newItem.selectDate = formattedDate;
    return newItem;
  });

  //user-permissions
  const canEditReminder = usePermission("Reminders", "Edit");
  const canCancelReminder = usePermission("Reminders", "Cancel");
  const canCompleteReminder = usePermission("Reminders", "Complete");

  return (
    <div>
      {reminderList?.map((item, index) => (
        <div className="reminder-card-container">
          <div className="calendar-reminder-card">
            <div className="reminder-card-header" key={item.reminderId}>
              <div className="reminder-heading">
                <p className="mb-0">
                  <span className="reminder-card-title">{item.title}</span>
                  {item.reminderTag === "General Reminder" && (
                    <span className="reminder-badge">General Reminder</span>
                  )}
                </p>
              </div>
              <div className="">
                {statusLoading && reminderId === item.reminderId ? (
                  <Spinner size="sm" variant="secondary" />
                ) : (
                  <Dropdown className="new-custom-dropdown">
                    {item.statusId !== enumList.statusEnum.value.Completed &&
                      item.statusId !== enumList.statusEnum.value.Cancelled && (
                        <Dropdown.Toggle variant="" id="dropdown-basic">
                          <ThreeDotAction />
                        </Dropdown.Toggle>
                      )}

                    <Dropdown.Menu>
                      {canCompleteReminder && (
                        <Dropdown.Item>
                          <div
                            className="d-flex align-items-center"
                            onClick={() =>
                              handleStatus(item.reminderId, "complete")
                            }
                          >
                            <TickIconCircle />{" "}
                            <span className="ms-1  dropdown-title">
                              Mark as Completed
                            </span>
                          </div>
                        </Dropdown.Item>
                      )}
                      {canEditReminder && (
                        <Dropdown.Item>
                          <div
                            className="d-flex align-items-center"
                            onClick={() => editReminder(item)}
                          >
                            <EditPenIconSquare />{" "}
                            <span className="ms-1 dropdown-title">
                              Edit Reminder
                            </span>
                          </div>
                        </Dropdown.Item>
                      )}
                      {canCancelReminder && (
                        <Dropdown.Item>
                          <div
                            className="d-flex align-items-center"
                            onClick={() => {
                              handleStatus(item.reminderId, "cancel");
                            }}
                          >
                            <CancelSquareIcon />{" "}
                            <span className="ms-1 dropdown-title">
                              Cancel Reminder
                            </span>
                          </div>
                        </Dropdown.Item>
                      )}
                    </Dropdown.Menu>
                  </Dropdown>
                )}
              </div>
            </div>
            {item.inquiryInfoId ? (
              <div className="summary-title d-flex align-items-center ">
                <span>
                  <svg
                    width="13"
                    height="14"
                    viewBox="0 0 13 14"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M10.8334 11.875V10.7917C10.8334 10.217 10.6051 9.66593 10.1988 9.2596C9.79248 8.85327 9.24138 8.625 8.66675 8.625H4.33341C3.75878 8.625 3.20768 8.85327 2.80135 9.2596C2.39502 9.66593 2.16675 10.217 2.16675 10.7917V11.875"
                      stroke="#58596B"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                    />
                    <path
                      d="M6.49992 6.45833C7.69654 6.45833 8.66659 5.48828 8.66659 4.29167C8.66659 3.09505 7.69654 2.125 6.49992 2.125C5.3033 2.125 4.33325 3.09505 4.33325 4.29167C4.33325 5.48828 5.3033 6.45833 6.49992 6.45833Z"
                      stroke="#58596B"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                    />
                  </svg>
                </span>
                <span className="ms-2"> {item.guestName}</span>
                <span className="ms-2 me-2">|</span>
                <span className="me-1">{item.eventDescription} -</span>
                {item.company && <span>{item.company}-</span>}
                {item.brideName && item.groomName && (
                  <span>{item.brideName + " & " + item.groomName}</span>
                )}
              </div>
            ) : null}

            <div className="calender-reminder-desc">{item.description}</div>
            <div className="d-flex align-items-center justify-content-between">
              <div className="d-flex align-items-center">
                <CalenderIconGrey />{" "}
                <div className="ms-1 reminder-date">
                  {formatDate(item.selectDate, "DD MMM, YYYY")} |{" "}
                  {item.selectTime}
                </div>
              </div>
              {item.statusId === enumList.statusEnum.value.Upcoming ? (
                <div className="status status-bg-yellow txt-yellow">
                  Upcoming
                </div>
              ) : item.statusId === enumList.statusEnum.value.Completed ? (
                <div className="status status-bg-success txt-success-light">
                  Completed
                </div>
              ) : item.statusId === enumList.statusEnum.value.Cancelled ? (
                <div className="status status-bg-danger txt-danger">
                  Cancelled
                </div>
              ) : (
                ""
              )}
            </div>
          </div>
          {index !== remainders.length - 1 && (
            <div className="card-border mb-3"></div>
          )}
        </div>
      ))}
      <CalendarAddReminder
        setShow={setShowAddReminder}
        show={showAddReminderCanvas}
        editData={editData}
      />
      <CancelReminderModal
        show={showConfirmModal}
        setShow={setShowConfirmModal}
        onConfirm={changeReminderStatus}
      />
      <MarkCompleteReminderModal
        show={showMarkCompleteModal}
        setShow={setShowMarkCompleteModal}
        onConfirm={changeReminderStatus}
      />
    </div>
  );
};

export default CalendarReminders;
