import React from "react";
import design from "../../../../../assets/img/summary-date.png";
import { enumList } from "../../../../../utils/enumList";

const SummaryMonthStats = ({ allVenueList, selectedVenue, venueDetails }) => {
  const getData = (monthSlots = [], workingDaysList) => {
    return monthSlots?.filter((k) => !workingDaysList.includes(k.day)).length;
  };

  const total = allVenueList?.reduce(
    (acc, venue) => {
      if (
        selectedVenue === venue.id ||
        selectedVenue === -1 ||
        !selectedVenue
      ) {
        const dayMapping = {
          [enumList.calenderDays.text.SUN]: 0,
          [enumList.calenderDays.text.MON]: 1,
          [enumList.calenderDays.text.TUE]: 2,
          [enumList.calenderDays.text.WED]: 3,
          [enumList.calenderDays.text.THU]: 4,
          [enumList.calenderDays.text.FRI]: 5,
          [enumList.calenderDays.text.SAT]: 6,
        };

        let working = venue.workingDays?.map(({ day, timeSlots }) => {
          const hasTimeSlot = timeSlots.some(
            ({ startTime, endTime }) => startTime && endTime
          );
          return hasTimeSlot ? dayMapping[day] : null;
        });

        const workingDaysList =
          working
            ?.filter((k) => k !== null)
            .map((key) => enumList.calenderDays.valueFull[key]) || [];

        venue.monthSlots?.forEach((slot) => {
          acc.totalBookings += slot.bookings?.length || 0;
          acc.operationalCount +=
            slot.notOperational.filter(({ reasonTypeId }) => reasonTypeId === 2)
              ?.length || 0;

          if (workingDaysList.includes(slot.day)) {
            acc.closedCount +=
              slot.notOperational.filter(
                ({ reasonTypeId }) => reasonTypeId === 1
              )?.length || 0;
          }
        });

        acc.closedCount += getData(venue.monthSlots, workingDaysList);
      }
      return acc;
    },
    { totalBookings: 0, closedCount: 0, operationalCount: 0 }
  );

  return (
    <div className="summary-detail-groups flex-column d-flex pt-4">
      <div>
        <div className="day-stats">
          <h6 className="month-heading mb-3">Month Stats</h6>
          <div className="row">
            <div className="col-4 month-info">
              <h6>{total.totalBookings}</h6>
              <p>Bookings</p>
            </div>
            <div className="col-4 month-info">
              <h6>{total.closedCount}</h6>
              <p>Closed</p>
            </div>
            <div className="col-4 month-info">
              <h6>{total.operationalCount}</h6>
              <p>Not Operational</p>
            </div>
          </div>
        </div>
      </div>

      <div className="summary-detail-wrapper mt-5">
        <img src={design} alt="" />
        <p className="description mt-2 no-data-summary">
          Select date for summary
        </p>
      </div>
    </div>
  );
};

export default SummaryMonthStats;
