import React, { useEffect, useState } from "react";
import cheers from "../../../../../../../../assets/img/cheers.png";
import fastFood from "../../../../../../../../assets/img/fast-food.png";
import tray from "../../../../../../../../assets/img/tray.png";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import inquiryBookingServices from "../../../../../../../../services/api-services/inquiryBookingServices";
import apiStatus from "../../../../../../../../utils/apiResponseHandler";
import { toast } from "react-toastify";
import { v4 as uuId } from "uuid";

const SelectPackagesType = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const [inquiryInfo, setInquiryInfo] = useState(null);
  const params = useParams();

  useEffect(() => {
    if (!inquiryInfo) {
      getInquiryInfo();
    }
  }, []);

  const getInquiryInfo = async () => {
    try {
      const response = await inquiryBookingServices.getInquiryById(
        params.inquiryId
      );
      const { title, data } = response;
      if (title === apiStatus.success) {
        let customData = {
          ...data,
          inquirySlotOutDTO: data.inquirySlotOutDTO?.map((slot) => ({
            ...slot,
            slotTempId: uuId(),
          })),
        };
        setInquiryInfo(customData);
      }
    } catch (error) {
      toast.error(error.message);
    }
  };

  const currentSlot =
    inquiryInfo?.inquirySlotOutDTO?.find(
      (slot) => slot?.inquirySlotId == params?.slotId
    ) || {};

  return (
    <div className="select-packages-details text-center">
      <div className="content">
        <h3 className="title mb-3">Select Package Type</h3>
        <label className="form-label">
          choose a package or customize as per your requirements.
        </label>
        <div className="options-container">
          {currentSlot?.beveragePreferenceIds &&
            currentSlot?.foodPreferenceIds && (
              <div
                role="button"
                className="option"
                onClick={() => navigate(location.pathname + "/food-beverage")}
              >
                <div>
                  <img src={fastFood} alt="fast-food" />
                  <div className="option-label mt-4">Food + Beverage</div>
                </div>
              </div>
            )}
          {currentSlot?.foodPreferenceIds && (
            <div
              role="button"
              className="option"
              onClick={() => navigate(location.pathname + "/food")}
            >
              <div>
                <img src={tray} alt="tray" />
                <div className="option-label mt-4">Food</div>
              </div>
            </div>
          )}
          {currentSlot?.beveragePreferenceIds && (
            <div
              role="button"
              className="option"
              onClick={() => navigate(location.pathname + "/beverage")}
            >
              <div>
                <img src={cheers} alt="cheers" />
                <div className="option-label mt-4">Beverage</div>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default SelectPackagesType;
