import React, { useMemo, useState } from "react";
import "./style.scss";
import { Offcanvas } from "react-bootstrap";
import CustomDatePicker from "../../common/custom-date-picker/CustomDatePicker";
import { DropDownListComponent } from "@syncfusion/ej2-react-dropdowns";
import { enumList } from "../../../utils/enumList";
import CustomDropdown from "../../common/custom-dropdown/CustomDropdown";

export const RemindersFilterOffcanvas = ({
  reminderData,
  show,
  setShow,
  setFilterApplied,
  setFilteredData,
  setFilterCount,
  onClearFilter,
}) => {
  const [filterObject, setFilterObject] = useState({
    startDate: null,
    endDate: null,
    status: null,
    reminderType: null,
  });

  const handleChange = (e) => {
    const { value, name } = e.target;
    let object = { ...filterObject };
    object[name] = value;
    setFilterObject(object);
  };

  const handleChangeDate = (e, name) => {
    const value = e?.$d;
    let object = { ...filterObject };
    object[name] = value;
    setFilterObject(object);
  };

  const clearFilter = () => {
    setFilterObject({
      startDate: null,
      endDate: null,
      status: null,
      reminderType: null,
    });
    setShow(false);
    setFilterCount(0);
    onClearFilter();
  };

  const applyFilter = (e) => {
    e.preventDefault();
    filterReminder();
  };

  const filterReminder = () => {
    let newData = [
      ...reminderData.map((item) => {
        if (item.reminderTag === "") {
          return { ...item, reminderTag: "Inquiry Based" };
        } else {
          return item;
        }
      }),
    ];

    let count = 0;

    const { startDate, endDate, status, reminderType } = filterObject;

    if (startDate && endDate) {
      const startRange = new Date(startDate).setHours(0, 0, 0, 0);
      const endRange = new Date(endDate).setHours(23, 59, 59, 999);
      const filteredData = newData.filter((item) => {
        const createdDate = new Date(item.selectDate).setHours(0, 0, 0, 0);
        return createdDate >= startRange && createdDate <= endRange;
      });
      newData = filteredData;
      count++;
    }

    if (status) {
      const filtered = newData.filter((item) => {
        return status === item?.statusId;
      });
      newData = filtered;
      count++;
    }

    if (reminderType) {
      const filtered = newData.filter((item) => {
        console.log(reminderType, item.reminderTag);
        return (
          enumList.reminderTypeEnum.text[reminderType] === item?.reminderTag
        );
      });
      newData = filtered;
      count++;
    }

    if (count > 0) {
      setFilterApplied(true);
      setFilteredData(newData);
      setFilterCount(count);
      setShow(false);
    }
  };

  const statusList = useMemo(() => {
    let listItem = enumList.statusEnum.list;
    const { Upcoming, Completed, Cancelled, Today,Expired } = enumList.statusEnum.value;
    return listItem.filter((status) =>
      [Upcoming, Completed, Cancelled, Today,Expired].includes(status.value)
    );
  }, [enumList.statusEnum.list]);

  return (
    <form onSubmit={applyFilter}>
      <Offcanvas show={show} onHide={() => setShow(false)} placement="end">
        <Offcanvas.Header closeButton className="custom-modal">
          <Offcanvas.Title>Filters</Offcanvas.Title>
        </Offcanvas.Header>

        <Offcanvas.Body className="offcanvas-custom-body">
          <div className="offcanvas-body-container mt-2">
            <div className="row mx-2 ">
              <div className="col-md-12">
                <label className="form-label custom-form-label mb-0">
                  Date Range
                </label>
              </div>
              <div className="col-md-6 mb-4 dropdown-wrapper guest-mgmt-date ">
                <CustomDatePicker
                  placeholder="DD / MM / YYYY"
                  onChange={(e) => handleChangeDate(e, "startDate")}
                  value={filterObject.startDate}
                />
              </div>
              <div className="col-md-6 mb-4 dropdown-wrapper guest-mgmt-date">
                <CustomDatePicker
                  placeholder="DD / MM / YYYY"
                  onChange={(e) => handleChangeDate(e, "endDate")}
                  value={filterObject.endDate}
                />
              </div>
              <div className="col-md-12 mb-4 dropdown-wrapper">
                <CustomDropdown
                  dataSource={statusList}
                  placeholder={"Status"}
                  onChange={handleChange}
                  value={filterObject.status}
                  name="status"
                />
              </div>
              <div className="col-md-12 mb-4 dropdown-wrapper">
                <CustomDropdown
                  dataSource={enumList.reminderTypeEnum.list}
                  placeholder={"Type"}
                  onChange={handleChange}
                  value={filterObject.reminderType}
                  name="reminderType"
                />
              </div>
            </div>

            <div className="offCanvas-footer text-end p-3">
              <button
                className={`btn txt-primary btn-outline-light fw-bold me-3`}
                onClick={clearFilter}
              >
                Clear All
              </button>
              <button
                className="btn btn-primary px-4"
                onClick={applyFilter}
                disabled={
                  (!filterObject.endDate || !filterObject.startDate) &&
                  filterObject.status === "" &&
                  filterObject.reminderType === ""
                }
              >
                Apply
              </button>
            </div>
          </div>
        </Offcanvas.Body>
      </Offcanvas>
    </form>
  );
};
