import React, { useContext, useEffect, useState } from "react";
import { enumList } from "../../../../../../../../../utils/enumList";
import CustomDropdown from "../../../../../../../../common/custom-dropdown/CustomDropdown";
import { Accordion, Table } from "react-bootstrap";
import { Minus, Plus } from "../../../../../../../../../assets/SvgIcons";
import Spinner from "react-bootstrap/Spinner";
import ConfirmAndNext from "../../../../pop-up-modals/ConfirmAndNext";
import settingsService from "../../../../../../../../../services/api-services/settingsServices";
import apiStatus from "../../../../../../../../../utils/apiResponseHandler";
import { toast } from "react-toastify";
import { useParams } from "react-router-dom";
import { VenueDetailsContext } from "../../../VenueDetailsContext";

const FBPackageFoodForm = ({
  operationAction,
  setOperationAction,
  selectedFId,
  setSelectedFId,
}) => {
  const param = useParams();
  const edit = operationAction === enumList.action.edit;
  const { venueDetailsObject, permissions, reloadOverview } =
    useContext(VenueDetailsContext);
  const { isVenueDeleted } = venueDetailsObject;

  const [foodPackageObject, setFoodPackageObject] = useState({});
  const [foodPackageLoading, setFoodPackageLoading] = useState(true);
  const [foodPackageItems, setFoodPackageItems] = useState([]);
  const [foodPackageCategoryList, setFoodPackageCategoryList] = useState([]);
  const [foodPackageCategoryLoading, setFoodPackageCategoryLoading] =
    useState(true);
  const [selectedFoodPackageItemList, setSelectedFoodPackageItemList] =
    useState([]);

  const [confirmModalShow, setConfirmModalShow] = useState(false);
  const [disableSave, setDisableSave] = useState(true);
  const [isSaving, setIsSaving] = useState(false);

  useEffect(() => {
    const {
      venueFoodPackageName,
      venueFoodPackageCategoryId,
      foodPreferenceId,
      venueFoodPackageAmount,
    } = foodPackageObject;
    if (
      selectedFoodPackageItemList.length > 0 &&
      venueFoodPackageName &&
      venueFoodPackageCategoryId &&
      foodPreferenceId &&
      venueFoodPackageAmount
    ) {
      setDisableSave(false);
    } else {
      setDisableSave(true);
    }
  }, [selectedFoodPackageItemList, foodPackageObject]);

  useEffect(() => {
    getPackageCategoryData();
    getFoodPackageItems();
  }, []);

  useEffect(() => {
    if (selectedFId) getFoodPackageByPackageIdData(selectedFId);
  }, []);

  useEffect(() => {
    const dto = foodPackageObject?.venueFoodPackageItemMapDTO || [];
    if (dto.length > 0 && foodPackageItems.length > 0) {
      const idList = dto.map(
        ({ venueFoodPackageItemId }) => venueFoodPackageItemId
      );

      let selectedData = [];
      foodPackageItems.forEach((item) => {
        let selectedDTOData =
          dto.find(
            ({ venueFoodPackageItemId }) =>
              item.venueFoodPackageItemId === venueFoodPackageItemId
          ) || {};
        if (idList.includes(item.venueFoodPackageItemId)) {
          selectedData.push({
            ...item,
            count: selectedDTOData.count,
          });
        }
      });
      setSelectedFoodPackageItemList(selectedData);
    }
  }, [foodPackageItems, foodPackageObject?.venueFoodPackageItemMapDTO]);

  const getFoodPackageByPackageIdData = async (id) => {
    try {
      // setIsLoading(true);
      const response = await settingsService.getFoodPackageByFoodId(id);
      const { title, data } = response;

      if (title === apiStatus.success) {
        const {
          venueFoodPackageId,
          venueFoodPackageName,
          venueFoodPackageCategoryId,
          foodPreferenceId,
          venueFoodPackageAmount,
          venueFoodPackageItemMapDTO,
          venueFoodPackageCode,
        } = data[0];
        let obj = {
          venueFoodPackageId,
          venueFoodPackageName,
          venueFoodPackageCategoryId,
          foodPreferenceId,
          venueFoodPackageAmount,
          venueFoodPackageCode,
          venueFoodPackageItemMapDTO,
        };
        setFoodPackageObject(obj);
      }
    } catch (error) {
      toast.error(error.message);
    } finally {
      // setIsLoading(false);
    }
  };

  const getPackageCategoryData = async () => {
    try {
      setFoodPackageCategoryLoading(true);
      const response = await settingsService.getFoodPackageCategory();
      const { title, data = [] } = response;

      if (title === apiStatus.success) {
        setFoodPackageCategoryList(
          data.map(({ id, title }) => ({ text: title, value: id }))
        );
      }
    } catch (error) {
      toast.error(error.message);
    } finally {
      setFoodPackageCategoryLoading(false);
    }
  };

  const getFoodPackageItems = async () => {
    try {
      setFoodPackageLoading(true);
      const response = await settingsService.getFoodPackageItemListById(
        param.venueId
      );
      const { title, data } = response;

      if (title === apiStatus.success) {
        setFoodPackageItems(data);
      }
    } catch (error) {
      toast.error(error.message);
    } finally {
      setFoodPackageLoading(false);
    }
  };

  const inputChangeHandler = (e) => {
    const { value, name } = e.target;
    const data = { ...foodPackageObject };
    data[name] = value;
    setFoodPackageObject(data);
  };

  const addRemoveItem = (e, data) => {
    const { checked } = e.target;
    let selected = [...selectedFoodPackageItemList];
    if (checked) {
      selected.push({
        ...data,
        count: 1,
      });
    } else {
      selected = selected.filter(
        ({ venueFoodPackageItemId }) =>
          venueFoodPackageItemId !== data.venueFoodPackageItemId
      );
    }
    setSelectedFoodPackageItemList(selected);
  };

  const incAndDec = (type, venueFoodPackageItemId) => {
    setSelectedFoodPackageItemList(
      selectedFoodPackageItemList.map((data) => {
        if (data.venueFoodPackageItemId === venueFoodPackageItemId) {
          return {
            ...data,
            count: type === "inc" ? data.count + 1 : data.count - 1,
          };
        } else {
          return data;
        }
      })
    );
  };

  const handleSaveClick = async () => {
    try {
      setConfirmModalShow(false);
      setIsSaving(true);
      const {
        venueFoodPackageName,
        venueFoodPackageCategoryId,
        foodPreferenceId,
        venueFoodPackageAmount,
        venueFoodPackageId = 0,
      } = foodPackageObject;
      let payload = {
        venueFoodPackageId,
        venueFoodPackageName,
        venueFoodPackageCategoryId,
        venueFoodPackageCategoryName: foodPackageCategoryList.find(
          (list) => list.value === venueFoodPackageCategoryId
        ).text,
        foodPreferenceId,
        venueFoodPackageAmount,
        venueDetailId: param.venueId,
        venueFoodPackageItemMapDTO: selectedFoodPackageItemList.map(
          ({
            venueFoodItemTypeId,
            venueFoodItemTypeName,
            count,
            foodPreferenceId,
            venueFoodPackageItemId,
          }) => ({
            venueFoodItemTypeId,
            venueFoodItemTypeName:
              venueFoodItemTypeName +
              " " +
              enumList.FoodType.text[foodPreferenceId],
            count,
            venueFoodPackageItemId,
          })
        ),
      };
      const response = await settingsService.addFBPackageFood(
        param.venueId,
        payload
      );
      const { detail, title } = response;

      if (title === apiStatus.success) {
        setOperationAction(enumList.action.read);

        if (selectedFId) {
          getFoodPackageByPackageIdData(selectedFId);
        } else {
          getFoodPackageByPackageIdData(detail);
          setSelectedFId(detail);
        }
      }
    } catch (error) {
      toast.error(error.message);
    } finally {
      setIsSaving(false);
      reloadOverview();
    }
  };

  const discardClick = () => {
    setOperationAction(enumList.action.read);
    if (selectedFId) {
      getFoodPackageByPackageIdData(selectedFId);
      setOperationAction(enumList.action.read);
    } else {
      setOperationAction(enumList.action.list);
    }
  };

  const filteredOnFoodPreference = foodPackageItems.filter(
    ({ foodPreferenceId }) => {
      if (
        foodPackageObject.foodPreferenceId === enumList.FoodType.value.VegNonVeg
      ) {
        return (
          enumList.FoodType.value.Veg === foodPreferenceId ||
          enumList.FoodType.value.NonVeg === foodPreferenceId
        );
      }
      if (foodPackageObject.foodPreferenceId === enumList.FoodType.value.Veg) {
        return enumList.FoodType.value.Veg === foodPreferenceId;
      }
      if (
        foodPackageObject.foodPreferenceId === enumList.FoodType.value.NonVeg
      ) {
        return enumList.FoodType.value.NonVeg === foodPreferenceId;
      }
      if (
        foodPackageObject.foodPreferenceId === enumList.FoodType.value.Vegan
      ) {
        return enumList.FoodType.value.Vegan === foodPreferenceId;
      }
    }
  );

  const [_, __, VegNonVeg, ___] = enumList.FoodType.list;

  let foodTypeOptionList = venueDetailsObject.foodPreferenceIds
    ?.split(",")
    .map((item) => enumList.FoodType.list.find(({ value }) => value == item));

  if (
    venueDetailsObject.foodPreferenceIds.includes(
      enumList.FoodType.value.Veg
    ) &&
    venueDetailsObject.foodPreferenceIds.includes(
      enumList.FoodType.value.NonVeg
    )
  ) {
    foodTypeOptionList.push(VegNonVeg);
  }

  return (
    <div>
      <div className="title-wrapper mb-3">
        <h4>Food Package</h4>

        <div className="btn-wrapper d-flex align-items-center">
          {edit ? (
            <>
              <div
                role="button"
                className="txt-primary fw-bold me-3 btn-34"
                onClick={discardClick}
              >
                Discard
              </div>
              <button
                disabled={disableSave}
                className="btn btn-primary btn-34"
                onClick={() => setConfirmModalShow(true)}
              >
                {isSaving && (
                  <Spinner animation="border" size="sm" className="me-1" />
                )}
                Save Details
              </button>
            </>
          ) : (
            permissions?.canEdit && (
              <>
                <div
                  role="button"
                  className="txt-primary fw-bold btn-34"
                  onClick={() => setOperationAction(enumList.action.list)}
                >
                  Back
                </div>
                {!isVenueDeleted && (
                  <button
                    className="btn btn-outline-primary btn-34 ms-3"
                    onClick={() => setOperationAction(enumList.action.edit)}
                  >
                    Edit Details
                  </button>
                )}
              </>
            )
          )}
        </div>
      </div>

      <div className={edit ? "" : "readonly-form"}>
        <div className="row error-boundary">
          <div className="col-md-2 mb-3">
            <label className="form-label">Package Code</label>
            <label className="txt-dark fw-bold mt-1 d-block">
              {foodPackageObject.venueFoodPackageCode || "F-New"}
            </label>
          </div>
          <div className="col mb-3">
            <label className="form-label">Package Name *</label>
            <input
              type="text"
              className="form-control input-one-line"
              placeholder="Enter name"
              name="venueFoodPackageName"
              value={foodPackageObject.venueFoodPackageName}
              onChange={inputChangeHandler}
              readOnly={!edit}
            />
          </div>
          <div className="col mb-3">
            <label className="form-label">Package Category *</label>
            {!foodPackageCategoryLoading && (
              <CustomDropdown
                readonly={!edit}
                dataSource={foodPackageCategoryList}
                placeholder="Select category"
                onChange={inputChangeHandler}
                value={foodPackageObject.venueFoodPackageCategoryId}
                name="venueFoodPackageCategoryId"
              />
            )}
          </div>
          <div className="col mb-3">
            <label className="form-label">Food Preference *</label>
            <CustomDropdown
              readonly={!edit}
              dataSource={foodTypeOptionList}
              placeholder="Select category"
              onChange={(e) => {
                setSelectedFoodPackageItemList([]);
                inputChangeHandler(e);
              }}
              value={foodPackageObject.foodPreferenceId}
              name="foodPreferenceId"
            />
          </div>
          <div className="col mb-3">
            <label className="form-label">Price *</label>
            {edit ? (
              <input
                type="text"
                className="form-control input-one-line"
                name="venueFoodPackageAmount"
                placeholder="Enter Amount per plate"
                value={foodPackageObject.venueFoodPackageAmount}
                onChange={inputChangeHandler}
                readOnly={!edit}
              />
            ) : (
              <div className="py-1">
                {foodPackageObject.venueFoodPackageAmount} &nbsp; per plate
              </div>
            )}
          </div>
        </div>
      </div>

      {edit && foodPackageObject.foodPreferenceId && (
        <div className="mb-3 custom-accordian">
          <Accordion>
            <Accordion.Item eventKey="0">
              <Accordion.Header className="py-0">
                Package Items &nbsp;
                <span className="selected-count">
                  ( {selectedFoodPackageItemList.length}/
                  {filteredOnFoodPreference.length} )
                </span>
              </Accordion.Header>
              <Accordion.Body>
                {foodPackageLoading ? (
                  <div className="d-flex justify-content-center align-items-center py-3">
                    <Spinner
                      animation="border"
                      size="sm"
                      className="txt-primary"
                    />
                  </div>
                ) : filteredOnFoodPreference.length > 0 ? (
                  <div className="row">
                    {filteredOnFoodPreference.map((data, index) => (
                      <div className="col-md-3" key={index}>
                        <div className="mb-3 form-check">
                          <input
                            type="checkbox"
                            className="form-check-input custom-check-input fb-package-check"
                            checked={selectedFoodPackageItemList
                              .map(
                                ({ venueFoodPackageItemId }) =>
                                  venueFoodPackageItemId
                              )
                              .includes(data.venueFoodPackageItemId)}
                            onChange={(e) => addRemoveItem(e, data)}
                            disabled={!edit}
                            id={`food-checkbox-${index}`}
                          />
                          <label
                            className="form-check-label"
                            htmlFor={`food-checkbox-${index}`}
                          >
                            {data.venueFoodItemTypeName}&nbsp;{" "}
                            {foodPackageObject.foodPreferenceId ===
                              enumList.FoodType.value.VegNonVeg &&
                              enumList.FoodType.text[data.foodPreferenceId]}
                          </label>
                        </div>
                      </div>
                    ))}
                  </div>
                ) : (
                  <div className="text-center py-4">No Record Found</div>
                )}
              </Accordion.Body>
            </Accordion.Item>
          </Accordion>
        </div>
      )}

      {selectedFoodPackageItemList.length > 0 && (
        <Table responsive className="simple-table ">
          <thead className="bold-header">
            <tr>
              <th>PACKAGE ITEM NAME</th>
              <th className="count-width-col text-center">COUNT</th>
            </tr>
          </thead>
          <tbody>
            {selectedFoodPackageItemList.map((data) => {
              return (
                <tr>
                  <td>
                    {data.venueFoodItemTypeName}&nbsp;{" "}
                    {foodPackageObject.foodPreferenceId ===
                      enumList.FoodType.value.VegNonVeg &&
                      enumList.FoodType.text[data.foodPreferenceId]}
                  </td>
                  <td className="text-center">
                    {operationAction === enumList.action.read ? (
                      data.count
                    ) : (
                      <div className="add-on-count">
                        <div className="inc-dec-container">
                          <div
                            role={data.count > 1 ? "button" : "none"}
                            onDoubleClick={(e) => e.stopPropagation()}
                            onClick={(e) => {
                              e.stopPropagation();
                              if (data.count > 1)
                                incAndDec("dec", data.venueFoodPackageItemId);
                            }}
                          >
                            <Minus />
                          </div>
                          <div>{data.count}</div>
                          <div
                            role="button"
                            onDoubleClick={(e) => e.stopPropagation()}
                            onClick={(e) => {
                              e.stopPropagation();
                              incAndDec("inc", data.venueFoodPackageItemId);
                            }}
                          >
                            <Plus />
                          </div>
                        </div>
                      </div>
                    )}
                  </td>
                </tr>
              );
            })}
          </tbody>
        </Table>
      )}

      <ConfirmAndNext
        show={confirmModalShow}
        handleClose={() => setConfirmModalShow(false)}
        onSubmitClick={handleSaveClick}
        onDiscardClick={() => setConfirmModalShow(false)}
      />
    </div>
  );
};

export default FBPackageFoodForm;
