import React from "react";
import ConfirmImg from "../../../assets/img/confirm-img.png";
import { Modal } from "react-bootstrap";

export const ConfirmModal = ({ show, setShow, onConfirm, actionData }) => {
  const handleClose = () => {
    setShow(false);
  };

  return (
    <Modal show={show} className="custom-modal" centered onHide={handleClose}>
      <form onSubmit={onConfirm}>
        <Modal.Header closeButton>
          <Modal.Title>
            <h1 className="modal-title fs-5" id="exampleModalLabel">
              Confirm action
            </h1>
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="text-center">
            <img src={ConfirmImg} alt="" />
            <h4 className="fw-bold mt-4">Are you sure want to confirm ?</h4>
            <p className="description mb-3 text-center">
              {actionData.action === "complete" &&
                "Once marked as Complete, the reminder cannot be activated or edited again"}
              {actionData.action === "cancel" &&
                "Once Cancelled, the reminder cannot be activated or edited again"}
            </p>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <button className="btn btn-primary btn-sm" onClick={onConfirm}>
            Submit
          </button>
        </Modal.Footer>
      </form>
    </Modal>
  );
};
