import React, { useCallback, useContext, useEffect, useState } from "react";
import "../settings.scss";
import {
  AccessDashIconGrey,
  AccessTickIconPurple,
} from "../../../../assets/img/svg";
import SpinnerLoader from "../../../common/spinner/SpinnerLoader";
import settingsService from "../../../../services/api-services/settingsServices";
import apiStatus from "../../../../utils/apiResponseHandler";
import { toast } from "react-toastify";
import { ProfileContext } from "../../../../App";
import { enumList } from "../../../../utils/enumList";

const Access = () => {
  const [accessList, setAccessList] = useState([]);
  const [loading, setLoading] = useState(false);
  const [perLoading, setPerLoading] = useState(false);
  const [loaderKey, setLoaderKey] = useState(false);
  const [apiData, setApiData] = useState([]);
  const { profileDetails } = useContext(ProfileContext);

  useEffect(() => {
    setLoading(true);
    fetchAccessList();
  }, []);

  const toggleValue = (key, feature, role, access) => {
    setLoaderKey(key + feature + role);
    const roleId = apiData.find((data) => data.roleName === role)?.roleId;
    const roleData = apiData
      .find((data) => data.roleName === role)
      ?.rolePermissions.find((per) => per.featureName === feature);

    if (roleData) {
      const permission = roleData.permissions.find(
        (perm) => perm.permissionName === key
      );
      if (permission) {
        updatePermissions(
          roleId,
          roleData.featureId,
          permission.permissionId,
          access
        );
      }
    }
  };

  const featureTemplate = (feature) => (
    <div className="txt-dark-grey fw-bold p-1">{feature}</div>
  );

  const actionsTemplate = (actions) => (
    <div className="d-flex flex-column align-items-start">
      {actions.map((action, index) => (
        <div key={index} className="mb-3 txt-grey text-center p-1">
          {action}
        </div>
      ))}
    </div>
  );

  const RoleTemplate = (props, role, roleId) => {
    return Object.keys(props[role]).map((key, index) => (
      <div className="mb-3 w-100 access-icon text-center p-1" key={index}>
        {perLoading && key + props.feature + role === loaderKey ? (
          <div
            className="spinner-border spinner-border-sm txt-primary"
            role="status"
            aria-hidden="true"
          ></div>
        ) : props[role][key] ? (
          <span
            onClick={
              roleId === enumList.userRolesEnum.value.Administrator ||
              key === "View" ||
              props.feature === "UserManagement"
                ? undefined
                : () => toggleValue(key, props.feature, role, false)
            }
            className="hover-row p-2 rounded-5"
          >
            <AccessTickIconPurple />
          </span>
        ) : (
          <span
            onClick={
              roleId === enumList.userRolesEnum.value.Administrator
                ? undefined
                : () => toggleValue(key, props.feature, role, true)
            }
          >
            <AccessDashIconGrey />
          </span>
        )}
      </div>
    ));
  };

  const fetchAccessList = async () => {
    try {
      const response = await settingsService.getAccessPermissionList();
      const { data, detail, title } = response;
      if (title === apiStatus.success) {
        setApiData(data);
        transformPermissions(data);
      }
    } catch (error) {
      toast.error(error.message);
    } finally {
      setLoading(false);
    }
  };

  const updatePermissions = async (role_id, fId, permId, access) => {
    const payload = {
      featureId: fId,
      permissionId: permId,
      isAllowed: access,
      roleId: role_id,
    };
    try {
      setPerLoading(true);
      const response = await settingsService.updateAccessPermission(payload);
      const { data, detail, title } = response;
      if (title === apiStatus.success) {
        fetchAccessList();
      }
    } catch (error) {
      toast.error(error.message);
    } finally {
      setPerLoading(false);
    }
  };

  const transformPermissions = (rolesData) => {
    const transformedData = [];

    rolesData?.forEach((role) => {
      role.rolePermissions
        .sort((a, b) => a.permissionId - b.permissionId)
        .forEach((permissionGroup) => {
          const { featureName } = permissionGroup;
          const permissions = permissionGroup.permissions.sort(
            (a, b) => a.permissionId - b.permissionId
          );
          const existingFeature = transformedData.find(
            (item) => item.feature === featureName
          );

          if (!existingFeature) {
            transformedData.push({
              feature: featureName,
              actions: permissions.map((perm) => perm.permissionName.trim()),
              [role.roleName]: permissions.reduce((acc, perm) => {
                acc[perm.permissionName.trim()] = perm.isAllowed;
                return acc;
              }, {}),
            });
          } else {
            existingFeature[role.roleName] = permissions.reduce((acc, perm) => {
              acc[perm.permissionName.trim()] = perm.isAllowed;
              return acc;
            }, {});
          }
        });
    });
    setAccessList(transformedData);
  };

  return (
    <div className="h-100 overflow-x-auto">
      {loading ? (
        <SpinnerLoader />
      ) : (
        <table className="table simple-table access-table ">
          <thead>
            <tr>
              <th>Features</th>
              <th>Actions</th>
              {apiData.map((role) => (
                <th key={role.roleName} className="text-center">
                  {role.roleName}
                </th>
              ))}
            </tr>
          </thead>
          <tbody>
            {accessList.map((item, index) => (
              <tr key={index}>
                <td>{featureTemplate(item.feature)}</td>
                <td>{actionsTemplate(item.actions)}</td>
                {apiData.map((role) => (
                  <td key={role.roleId}>
                    {RoleTemplate(item, role.roleName, role.roleId)}
                  </td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
};

export default Access;
