import { TimePicker } from "antd";
import dayjs from "dayjs";

const CustomTimePicker = ({ value, handleChange, selectedDate }) => {
  const disabledTime = () => {
    if (selectedDate && dayjs(selectedDate).isSame(dayjs(), "day")) {
      const currentHour = dayjs().hour();
      const currentMinute = dayjs().minute();

      return {
        disabledHours: () =>
          Array.from({ length: 24 }, (_, i) => i).filter(
            (hour) => hour < currentHour
          ),

        disabledMinutes: (selectedHour) => {
          if (selectedHour === currentHour) {
            return Array.from({ length: 60 }, (_, i) => i).filter(
              (minute) => minute <= currentMinute
            );
          }
          return [];
        },
      };
    }
    return {};
  };

  return (
    <div className="reminder-time-picker">
      <TimePicker
        value={value ? dayjs(value, "hh:mm A") : null}
        onChange={handleChange}
        name="selectTime"
        format="hh:mm A"
        placeholder="00:00 AM / PM"
        className={`custom-time-select`}
        disabledTime={disabledTime}
        disabled={!selectedDate}
      />
    </div>
  );
};

export default CustomTimePicker;
