import React from "react";
import { Button, Modal } from "react-bootstrap";
import design from "../../../../../../assets/img/delete-payment-info.png";

const DeletePaymentModal = ({ handleClose, show, onConfirm }) => {
  return (
    <Modal show={show} onHide={handleClose} className="custom-modal" centered>
      <Modal.Header closeButton>
        <Modal.Title>Information Action</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div className="d-flex flex-column align-items-center">
          <img src={design} alt="" className="mb-2" height={130} width={130} />
          <div className="text-center">
            <h4 className="fw-bold mb-4 mt-3 fs-24-px">
              Are you sure want to confirm ?
            </h4>
            <p className="description mb-3 text-center fs-16-px">
              Dorem ipsum dolor sit amet, consectetur adipiscing elit. Nunc
              vulputate libero et velit interdum, ac aliquet odio mattis class
              aptent.
            </p>
          </div>
        </div>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="primary" className="btn-40" onClick={onConfirm}>
          Confirm
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default DeletePaymentModal;
