import React, { useState } from "react";
import "./notification.scss";
import Offcanvas from "react-bootstrap/Offcanvas";
import TodaysNotification from "./TodaysNotification";
import WeekNotification from "./WeekNotification";
import MonthNotification from "./MonthNotification";

const Notification = ({ isExtendedSidebar, show, onHideNotification }) => {

  const [activeTab, setActiveTab] = useState("#today");
  let tabs = [
    { id: "#today", title: "Today" },
    { id: "#this-week", title: "This Week" },
    { id: "#this-month", title: "This Month" },
  ];

  const handleTabChange = (id) => {
    setActiveTab(id);
  };

  return (
    <Offcanvas
      className={`notification-offcanvas custom-modal ${
        isExtendedSidebar
          ? "notification-offcanvas-230px"
          : "notification-offcanvas-75px"
      }  `}
      placement="start"
      backdropClassName={`${
        isExtendedSidebar
          ? "notificatio-sidebar-230px"
          : "notificatio-sidebar-75px"
      } `}
      show={show}
      onHide={onHideNotification}
    >
      <Offcanvas.Header closeButton>
        <Offcanvas.Title>Notification</Offcanvas.Title>
      </Offcanvas.Header>
      <Offcanvas.Body className="p-4 notification-list">
        <ul className="nav nav-tabs custom-nav-tabs d-flex gap-3 border-0 mb-4">
          {tabs.map((item, index) => (
            <li className="nav-item" key={index}>
              <span
                className={`nav-link cursor-pointer ${
                  item.id === activeTab ? "active" : ""
                }`}
                onClick={() => handleTabChange(item.id)}
              >
                {item.title}
              </span>
            </li>
          ))}
        </ul>

        <div className="notification-list overflow-auto">
          {activeTab === "#today" && <TodaysNotification />}
          {activeTab === "#this-week" && <WeekNotification />}
          {activeTab === "#this-month" && <MonthNotification />}
        </div>
      </Offcanvas.Body>
    </Offcanvas>
  );
};

export default Notification;
