import {
  ColumnDirective,
  ColumnsDirective,
  GridComponent,
} from "@syncfusion/ej2-react-grids";
import { renderUserStatus } from "../../../../utils/CommonUtils";
import { useNavigate } from "react-router-dom";
import SpinnerLoader from "../../../common/spinner/SpinnerLoader";
import { enumList } from "../../../../utils/enumList";

const Users = ({ usersList, loading }) => {
  const navigate = useNavigate();

  const userStatus = (props) => {
    return renderUserStatus(props.statusName);
  };

  const primaryUser = ({ userRoleName, userRoleId }) => {
    if (userRoleId !== enumList.userRolesEnum.value.Administrator)
      return userRoleName;
    else
      return (
        <div className="d-flex flex-row">
          <div>{userRoleName}</div>
          <div className="account-primary-badge ms-2">Primary</div>
        </div>
      );
  };

  const onRowSelectHandle = (args) => {
    navigate(`/settings/user-management/user-profile/${args.data.id}`);
  };

  const columnConfig = [
    { field: "id", headerText: "USER ID" },
    { field: "firstName", headerText: "FIRST NAME" },
    { field: "lastName", headerText: "LAST NAME" },
    { field: "mobileNumber", headerText: "MOBILE NUMBER" },
    {
      field: "emailAddress",
      headerText: "EMAIL ADDRESS",
      template: ({ emailAddress }) => (
        <div className="text-truncate" title={emailAddress}>{emailAddress}</div>
      ),
    },
    { field: "userRoleName", headerText: "ACCESS", template: primaryUser },
    {
      field: "status",
      headerText: "STATUS",
      template: userStatus,
    },
  ];

  return (
    <>
      {loading ? (
        <SpinnerLoader />
      ) : (
        <div className="users-table custom-data-table sub-plan-table">
          <GridComponent dataSource={usersList} rowSelected={onRowSelectHandle}>
            <ColumnsDirective>
              {columnConfig.map((col, index) => (
                <ColumnDirective key={index} {...col} width={200}/>
              ))}
            </ColumnsDirective>
          </GridComponent>
        </div>
      )}
    </>
  );
};

export default Users;
