import React from "react";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import img from "../../../../../assets/img/delete-note-img.png";

const DeleteUserModal = ({ show, setShow, onConfirm }) => {
  const handleClose = () => setShow(false);

  return (
    <Modal show={show} onHide={handleClose} className="custom-modal" centered>
      <Modal.Header closeButton>
        <Modal.Title>Account Action</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div className="inactive-account-modal-wrapper text-center">
          <img src={img} alt="" />
          <h4 className="fw-bold">Delete Action?</h4>
          <p className="description mb-3 text-center">
            Once deleted the action cannot be reversed.
            <br /> The user will not be able to access the account.
          </p>
        </div>
      </Modal.Body>
      <Modal.Footer className="gap-2">
        <div
          className="fw-bold txt-primary me-2 cursor-pointer "
          onClick={() => {
            setShow(false);
          }}
        >
          Discard
        </div>
        <Button
          variant="danger"
          onClick={() => {
            onConfirm();
            setShow(false);
          }}
        >
          Confirm
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default DeleteUserModal;
