import React, { useEffect, useState } from "react";
import Users from "./Users";
import Access from "./Access";
import UserManagementHeader from "./UserManagementHeader";
import "../settings.scss";
import { useNavigate, useSearchParams } from "react-router-dom";
import settingsService from "../../../../services/api-services/settingsServices";
import apiStatus from "../../../../utils/apiResponseHandler";
import { toast } from "react-toastify";

const UserManagement = () => {
  let tabs = [
    { id: "users", title: "Users" },
    { id: "access", title: "Access" },
  ];

  const [searchParams] = useSearchParams();
  const navigate = useNavigate();

  const defaultTab = searchParams.get("tab") || "users";
  const [activeTab, setActiveTab] = useState(defaultTab);

  useEffect(() => {
    navigate(`?tab=${activeTab}`, { replace: true });
  }, [activeTab, navigate]);

  const [loading, setLoading] = useState(false);
  const [usersList, setUsersList] = useState([]);

  useEffect(() => {
    getUsersList();
  }, []);

  const getUsersList = async () => {
    try {
      setLoading(true);
      const response = await settingsService.getUsersDetailsList();
      const { title, data, detail } = response;
      if (detail === apiStatus.success) {
        setUsersList(data);
      }
    } catch (error) {
      toast.error(error.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <>
      <header className="header px-4">
        <UserManagementHeader getUsersList={getUsersList} />
      </header>

      <ul className="nav nav-tabs custom-nav-tabs d-flex gap-3 mb-4 mx-4">
        {tabs.map((item, index) => (
          <li className="nav-item" key={index}>
            <span
              className={`nav-link ${activeTab === item.id ? "active" : ""}`}
              onClick={() => setActiveTab(item.id)}
              role="button"
            >
              <span className="ms-1">{item.title}</span>
            </span>
          </li>
        ))}
      </ul>

      <div className="user-management">
        <div className="flex-grow-1 mx-4">
          <div className="tab-content bg-custom-white h-100">
            {activeTab === "users" && (
              <Users usersList={usersList} loading={loading} />
            )}
            {activeTab === "access" && <Access />}
          </div>
        </div>
      </div>
    </>
  );
};

export default UserManagement;
