import React, { useEffect, useState } from "react";
import "./style.scss";
import { Offcanvas, Spinner } from "react-bootstrap";
import { TimePicker } from "antd";
import CustomDatePicker from "../../common/custom-date-picker/CustomDatePicker";
import dayjs from "dayjs";
import remindersServices from "../../../services/api-services/reminderServices";
import apiStatus from "../../../utils/apiResponseHandler";
import CustomTimePicker from "../../common/reminder-timepicker/CustomTimePicker";

export const AddRemindersOffcanvas = ({
  show,
  setShow,
  getList,
  editData,
  setEditData,
}) => {
  const defaultState = {
    title: "",
    description: "",
    selectDate: "",
    selectTime: "",
    remindBeforeTime: "",
    reminderTag: "",
  };
  const [reminder, setReminder] = useState({ ...defaultState });
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (editData) {
      setReminder({ ...editData });
    }
  }, [editData]);

  const inputChangeHandler = (e) => {
    const { value, name } = e.target;
    const newReminder = { ...reminder };
    newReminder[name] = value;
    setReminder(newReminder);
  };

  const formatTime = (timeString) => {
    if (timeString) {
      const [time, modifier] = timeString.split(" ");
      let [hours, minutes] = time.split(":");
      hours = parseInt(hours, 10);
      minutes = parseInt(minutes, 10);
      return `${hours < 10 ? "0" + hours : hours}:${
        minutes < 10 ? "0" + minutes : minutes
      } ${modifier}`;
    }
  };

  const handleDatePickerChange = (e, name) => {
    if (e) {
      const date = e?.$d;
      setReminder({ ...reminder, [name]: date });
    } else {
      setReminder({ ...reminder, [name]: e });
    }
  };

  const handleTimePickerChange = (e, name) => {
    const time = e?.$d;
    if (name === "remindBeforeTime" && time) {
      const hours = ("0" + time.getHours()).slice(-2);
      const minutes = ("0" + time.getMinutes()).slice(-2);
      const formatted = hours + ":" + minutes;
      formatted !== "00:00" && setReminder({ ...reminder, [name]: formatted });
    } else if (name === "selectTime" && time) {
      const date = time;
      setReminder({
        ...reminder,
        [name]: formatTime(date.toLocaleTimeString("en-US")),
      });
    } else {
      e === null &&
        setReminder({
          ...reminder,
          [name]: e,
        });
    }
  };

  const addNewReminder = async () => {
    setLoading(true);
    try {
      const payload = {
        reminderId: 0,
        inquiryInfoId: null,
        guestId: 0,
        isEdited: true,
        ...reminder,
        selectDate: dayjs(reminder.selectDate).format("DD-MM-YYYY"),
      };
      const { title } = await remindersServices.addGeneralReminder(payload);
      if (title === apiStatus.success) {
        setShow(false);
        setReminder({
          ...defaultState,
        });
        setEditData(null);

        getList();
      }
    } catch (error) {
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  const cancelHandle = () => {
    setReminder({
      ...defaultState,
    });
    setShow(false);
    setEditData(null);
  };

  return (
    <form>
      <Offcanvas show={show} onHide={cancelHandle} placement={"end"}>
        <Offcanvas.Header closeButton className="custom-modal">
          <Offcanvas.Title>Add Reminder</Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body className="offcanvas-custom-body">
          <div className="offcanvas-body-content p-3">
            <h6 className="mb-4">Create general reminder</h6>
            <div className="row">
              <div className="col-md-12 mb-4">
                <label for="" className="form-label custom-form-label">
                  Title *
                </label>
                <input
                  type="text"
                  className="form-control input-one-line"
                  value={reminder.title}
                  onChange={inputChangeHandler}
                  name="title"
                />
              </div>
            </div>
            <div className="row">
              <div className="col-md-12 mb-4">
                <label for="" className="form-label custom-form-label">
                  Description
                </label>
                <input
                  type="text"
                  className="form-control input-one-line"
                  value={reminder.description}
                  onChange={inputChangeHandler}
                  name="description"
                />
              </div>
            </div>
            <div className="row">
              <div className="col-md-6 mb-4">
                <label for="" className="form-label custom-form-label">
                  Select Date *
                </label>
                <CustomDatePicker
                  name="selectDate"
                  value={reminder.selectDate}
                  onChange={(e) => handleDatePickerChange(e, "selectDate")}
                  placeholder="DD / MM / YY"
                  minDate={dayjs(new Date())}
                />
              </div>
              <div className="col-md-6 mb-4">
                <label for="" className="form-label custom-form-label">
                  Time *
                </label>
                <CustomTimePicker
                  value={reminder.selectTime}
                  handleChange={(e) => handleTimePickerChange(e, "selectTime")}
                  selectedDate={reminder.selectDate}
                />
              </div>
            </div>
            <div className="row">
              <div className="col-md-6 mb-4">
                <label for="" className="form-label custom-form-label">
                  Remind me Before *
                </label>
                <TimePicker
                  value={
                    reminder.remindBeforeTime
                      ? dayjs(reminder.remindBeforeTime, "hh:mm")
                      : null
                  }
                  onChange={(e) =>
                    handleTimePickerChange(e, "remindBeforeTime")
                  }
                  name="remindBeforeTime"
                  format={"HH:mm"}
                  placeholder="00:00 AM / PM"
                  className={`custom-time-select`}
                  
                />
              </div>
            </div>
          </div>

          <div className="offcanvas-footer-content w-100">
            <div className="d-flex w-100 align-items-center justify-content-end  ">
              <button
                className={`btn txt-primary btn-outline-light fw-bold me-3`}
                onClick={cancelHandle}
              >
                Cancel
              </button>
              <div>
                <button
                  className="btn btn-primary px-4"
                  type="submit"
                  disabled={
                    reminder.title === "" ||
                    !reminder.selectDate ||
                    !reminder.selectTime ||
                    !reminder.remindBeforeTime
                  }
                  onClick={addNewReminder}
                >
                  {loading && (
                    <Spinner animation="border" size="sm" className="me-1" />
                  )}
                  {editData ? "Update" : "Save"}
                </button>
              </div>
            </div>
          </div>
        </Offcanvas.Body>
      </Offcanvas>
    </form>
  );
};
