import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import img from "../../../../../../assets/img/confirm-img.png";
import "./style.scss";
import inquiryBookingServices from "../../../../../../services/api-services/inquiryBookingServices";
import settingsService from "../../../../../../services/api-services/settingsServices";
import { useEffect, useState } from "react";
import { toast } from "react-toastify";
import { enumList } from "../../../../../../utils/enumList";
import apiStatus from "../../../../../../utils/apiResponseHandler";
import CustomDropdown from "../../../../../common/custom-dropdown/CustomDropdown";
import { Spinner } from "react-bootstrap";

const InquiryReassignModal = ({ show, setShow, inquiryId, getInquiryInfo }) => {
  const [assignUserList, setAssignUserList] = useState([]);
  const [leadOwnerId, setLeadOwnerId] = useState(null);
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    getAssignUserList();
  }, []);

  const reassignInquiryToOther = async () => {
    try {
      setIsLoading(true);
      const response = await inquiryBookingServices.reassignInquiry(
        inquiryId,
        leadOwnerId
      );
      const { title } = response;
      if (title === apiStatus.success) {
        handleClose();
        getInquiryInfo();
      }
    } catch (error) {
      toast.error(error.message);
    } finally {
      setIsLoading(false);
    }
  };

  const getAssignUserList = async () => {
    try {
      const response = await settingsService.getUsersDetailsList();
      const { detail, message, title, data } = response;
      if (title === apiStatus.success) {
        setAssignUserList(
          data
            ?.filter(
              (user) =>
                user.statusId === enumList.userAccountStatus.value.Active
            )
            .map((item) => ({
              value: item.id,
              text: item.firstName + " " + item.lastName,
            }))
        );
      } else {
        toast.error(detail || message);
      }
    } catch (error) {
      toast.error(error.message);
    }
  };

  const inputChangeHandler = (e) => {
    const { value } = e.target;
    setLeadOwnerId(value);
  };

  const handleClose = () => {
    setShow(false);
    setLeadOwnerId(null);
  };

  return (
    <div className="custom-modal">
      <Modal show={show} onHide={handleClose} className="custom-modal" centered>
        <Modal.Header closeButton>
          <Modal.Title>Confirmation</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="inquiry-reassign-modal">
            <img src={img} alt="" />
            <h4 className="heading">Re-assign the Inquiry</h4>
            <p className="description mb-3 text-center">
              Once you change the lead owner the previous lead owner wont be
              able to access the inquiry and perform any tasks
            </p>
            <p className="dropdown-header">Choose new Lead Owner</p>
            <div className="w-50">
              <CustomDropdown
                onChange={inputChangeHandler}
                value={leadOwnerId}
                name="leadOwnerId"
                dataSource={assignUserList}
                placeholder="Select"
              />
            </div>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Button
            variant="primary"
            onClick={reassignInquiryToOther}
            className=" fw-bolder fs-15"
            disabled={!leadOwnerId}
          >
            {isLoading && <Spinner animation="border" size="sm" />}
            <span className="ms-2">Confirm</span>
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default InquiryReassignModal;
