import React, { createContext, useContext, useEffect, useState } from "react";
import {
  AddNewButtonIcon,
  CancelSquareIcon,
  EditPenIconSquare,
  FilterIcon,
  SearchIcon,
  ThreeDotAction,
  TickIconCircle,
} from "../../../assets/img/svg";
import datedesign from "../../../assets/img/calendar-time.png";
import "./style.scss";
import { RemindersFilterOffcanvas } from "./RemindersFilterOffcanvas";
import { AddRemindersOffcanvas } from "./AddRemindersOffcanvas";
import { ConfirmModal } from "./ConfirmModal";
import { EditConfirmModal } from "./EditConfirmModal";
import remindersServices from "../../../services/api-services/reminderServices";
import apiStatus from "../../../utils/apiResponseHandler";
import SpinnerLoader from "../../common/spinner/SpinnerLoader";
import { enumList } from "../../../utils/enumList";
import { formatDate } from "../../../utils/JSutils";
import { Dropdown, Spinner } from "react-bootstrap";
import usePermission from "../../common/hooks/userPermission";

const ReminderContext = createContext();

const Reminders = () => {
  const [showAddReminder, setShowAddReminder] = useState(false);
  const [showReminderFilter, setShowReminderFilter] = useState(false);
  const [reminders, setReminders] = useState([]);
  const [loading, setLoading] = useState(false);
  const [editData, setEditData] = useState(null);
  const [searchValue, setSearchValue] = useState("");
  const [filteredData, setFilteredData] = useState([]);
  const [statusLoading, setStatusLoading] = useState(false);
  const [reminderId, setReminderId] = useState(null);
  const [filterApplied, setFilterApplied] = useState(false);
  const [filterCount, setFilterCount] = useState(0);
  const [showConfirmModal, setShowConfirmModal] = useState(false);
  const [actionData, setActionData] = useState({
    action: "",
    reminderId: "",
    statusId: "",
  });

  useEffect(() => {
    setLoading(true);
    getAllRemindersData();
  }, []);

  const getAllRemindersData = async () => {
    try {
      const { data, title } = await remindersServices.getAllReminderDetails();
      if (title === apiStatus.success) {
        let reminder = data?.map((item) => {
          const newItem = { ...item };
          const [day, month, year] = newItem?.selectDate?.split("-");
          const formattedDate = `${year}-${month}-${day}T00:00:00.000Z`;
          newItem.selectDate = formattedDate;
          return newItem;
        });
        setReminders(reminder);
        setFilteredData(reminder);
      }
    } catch (error) {
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  const editReminder = (data) => {
    setEditData(data);
    setShowAddReminder(true);
  };

  const inputSearchHandle = (value) => {
    setSearchValue(value);
    if (value.length) {
      const newData = reminders.filter((item) => {
        return (
          item.guestName.toLowerCase().includes(value.toLowerCase().trim()) ||
          item.eventDescription
            .toLowerCase()
            .includes(value.toLowerCase().trim()) ||
          item.title.toLowerCase().includes(value.toLowerCase().trim()) ||
          item.inquiryInfoId.toString().includes(value.toLowerCase().trim())
        );
      });
      setFilteredData(newData);
    } else {
      setFilteredData(reminders);
    }
  };

  const handleStatus = (id, action) => {
    let statusId = "";
    if (action === "complete") {
      statusId = enumList.statusEnum.value.Completed;
      setActionData({
        action: action,
        reminderId: id,
        statusId: statusId,
      });
    } else if (action === "cancel") {
      statusId = enumList.statusEnum.value.Cancelled;
      setActionData({
        action: action,
        reminderId: id,
        statusId: statusId,
      });
    }
    setShowConfirmModal(true);
  };

  const changesReminderStatus = async (e) => {
    e.preventDefault();
    setShowConfirmModal(false);
    try {
      setStatusLoading(true);
      setReminderId(actionData.reminderId);
      const { title } = await remindersServices.updateReminderStatus(
        actionData.reminderId,
        actionData.statusId
      );
      if (title === apiStatus.success) {
        getAllRemindersData();
      }
    } catch (error) {
      console.log(error);
    } finally {
      setStatusLoading(false);
    }
  };

  const data = filteredData;

  const todays = data.filter(
    (item) => item.statusId === enumList.statusEnum.value.Today
  );

  const upcoming = data.filter(
    (item) => item.statusId === enumList.statusEnum.value.Upcoming
  );

  const cancelled = data.filter(
    (item) => item.statusId === enumList.statusEnum.value.Cancelled
  );

  const completed = data.filter(
    (item) => item.statusId === enumList.statusEnum.value.Completed
  );

  const expired = data.filter(
    (item) => item.statusId === enumList.statusEnum.value.Expired
  );

  //user-permissions
  const canAddReminder = usePermission("Reminders", "Add");
  const canEditReminder = usePermission("Reminders", "Edit");
  const canCancelReminder = usePermission("Reminders", "Cancel");
  const canCompleteReminder = usePermission("Reminders", "Complete");

  return (
    <>
      <header className="header px-4">
        <h4 className="heading-title">Reminders</h4>
        <div className="flex-grow-1 d-flex justify-content-end">
          {canAddReminder && (
            <button
              className="btn px-3 btn-outline-primary d-flex align-items-center"
              onClick={() => setShowAddReminder(true)}
            >
              <AddNewButtonIcon />
              <span className="ms-1">Add New</span>
            </button>
          )}
        </div>
      </header>
      <div className="reminders">
        <div className="flex-grow-1 mx-4 bg-custom-white h-100">
          {loading ? (
            <SpinnerLoader />
          ) : (
            <>
              <div className="action-wrapper d-flex justify-content-between align-items-center mb-4 mx-2 flex-wrap">
                <h5 className="main-headings">Reminder Details </h5>
                <div className="search-wrapper-group d-flex align-items-center flex-wrap">
                  <div className="search-wrapper">
                    <SearchIcon />{" "}
                    <input
                      type="text"
                      placeholder="Search"
                      value={searchValue}
                      onChange={(e) => inputSearchHandle(e.target.value)}
                    />
                  </div>

                  <button
                    className="btn btn-34 btn-outline-secondary btn-sm filter-btn ms-3"
                    onClick={() => setShowReminderFilter(true)}
                  >
                    <FilterIcon />
                    <span className="ms-2">Filter</span>
                    {filterCount > 0 && (
                      <span className="filter-number">{filterCount}</span>
                    )}
                  </button>
                </div>
              </div>

              <div className="general-reminder-wrapper">
                <div className="d-flex flex-row h-100">
                  <ReminderContext.Provider
                    value={{
                      editReminder,
                      handleStatus,
                      statusLoading,
                      canCompleteReminder,
                      canCancelReminder,
                      canEditReminder,
                      reminderId,
                    }}
                  >
                    <TodaysReminder reminderList={todays} />
                    <Upcoming reminderList={upcoming} />
                    <Cancelled reminderList={cancelled} />
                    <Completed reminderList={completed} />
                    <Expired reminderList={expired} />
                  </ReminderContext.Provider>
                </div>
              </div>
            </>
          )}
        </div>
      </div>
      <RemindersFilterOffcanvas
        setShow={setShowReminderFilter}
        show={showReminderFilter}
        reminderData={reminders}
        setFilterCount={setFilterCount}
        onClearFilter={() => {
          setFilteredData(reminders);
        }}
        setFilteredData={setFilteredData}
        setFilterApplied={setFilterApplied}
      />
      <AddRemindersOffcanvas
        setShow={setShowAddReminder}
        show={showAddReminder}
        getList={getAllRemindersData}
        editData={editData}
        setEditData={setEditData}
      />
      <ConfirmModal
        show={showConfirmModal}
        setShow={setShowConfirmModal}
        onConfirm={changesReminderStatus}
        actionData={actionData}
      />
    </>
  );
};

export default Reminders;

const TodaysReminder = ({ reminderList = [] }) => {
  return (
    <div className="border-end h-100 reminder-item">
      <div className="reminder-group h-100">
        <h5 className="list-heading mb-4">
          Todays Reminders ({reminderList.length})
        </h5>
        <div className="reminder-container">
          {reminderList?.map((listItem) => {
            return listItem?.reminderTag ===
              enumList.reminderTypeEnum.text[1] ? (
              <GeneralReminder
                data={listItem}
                key={listItem?.reminderId}
                showAction={true}
              />
            ) : (
              <InquiryReminder
                data={listItem}
                key={listItem?.reminderId}
                showAction={true}
              />
            );
          })}
        </div>
      </div>
    </div>
  );
};

const Upcoming = ({ reminderList = [] }) => {
  return (
    <div className="border-end h-100 reminder-item">
      <div className="reminder-group h-100">
        <h5 className="list-heading mb-4">
          Upcoming ({reminderList?.length}){" "}
        </h5>

        <div className="reminder-container">
          {reminderList?.map((listItem) => {
            return listItem?.reminderTag ===
              enumList.reminderTypeEnum.text[1] ? (
              <GeneralReminder
                data={listItem}
                key={listItem?.reminderId}
                showAction={true}
              />
            ) : (
              <InquiryReminder
                data={listItem}
                key={listItem?.reminderId}
                showAction={true}
              />
            );
          })}
        </div>
      </div>
    </div>
  );
};

const Cancelled = ({ reminderList = [] }) => {
  return (
    <div className="border-end h-100 reminder-item">
      <div className="reminder-group h-100">
        <h5 className="list-heading mb-4">
          Cancelled ({reminderList?.length}){" "}
        </h5>

        <div className="reminder-container">
          {reminderList?.map((listItem) => {
            return listItem?.reminderTag ===
              enumList.reminderTypeEnum.text[1] ? (
              <GeneralReminder
                data={listItem}
                key={listItem?.reminderId}
                showAction={false}
              />
            ) : (
              <InquiryReminder
                data={listItem}
                key={listItem?.reminderId}
                showAction={false}
              />
            );
          })}
        </div>
      </div>
    </div>
  );
};

const Completed = ({ reminderList = [] }) => {
  return (
    <div className="h-100 reminder-item">
      <div className="reminder-group h-100">
        <h5 className="list-heading mb-4">
          Completed ({reminderList?.length}){" "}
        </h5>

        <div className="reminder-container">
          {reminderList?.map((listItem) => {
            return listItem?.reminderTag ===
              enumList.reminderTypeEnum.text[1] ? (
              <GeneralReminder
                data={listItem}
                key={listItem?.reminderId}
                showAction={false}
              />
            ) : (
              <InquiryReminder
                data={listItem}
                key={listItem?.reminderId}
                showAction={false}
              />
            );
          })}
        </div>
      </div>
    </div>
  );
};
const Expired = ({ reminderList = [] }) => {
  return (
    <div className="h-100 reminder-item">
      <div className="reminder-group h-100">
        <h5 className="list-heading mb-4">
          Expired ({reminderList?.length}){" "}
        </h5>

        <div className="reminder-container">
          {reminderList?.map((listItem) => {
            return listItem?.reminderTag ===
              enumList.reminderTypeEnum.text[1] ? (
              <GeneralReminder
                data={listItem}
                key={listItem?.reminderId}
                showAction={true}
              />
            ) : (
              <InquiryReminder
                data={listItem}
                key={listItem?.reminderId}
                showAction={true}
              />
            );
          })}
        </div>
      </div>
    </div>
  );
};

const GeneralReminder = ({ data, showAction }) => {
  const {
    editReminder,
    handleStatus,
    statusLoading,
    canCompleteReminder,
    canCancelReminder,
    canEditReminder,
    reminderId,
  } = useContext(ReminderContext);

  return (
    <div className="reminder-card">
      <div className="reminder-section">
        <div className="reminder-heading">
          <p className="mb-0">
            {data.title}
            <span className="reminder-badges">General Reminder</span>
          </p>
        </div>
        {showAction && (
          <div className="">
            <div className="d-flex align-items-center justify-content-between mb-2">
              {statusLoading && reminderId === data.reminderId ? (
                <Spinner size="sm" variant="secondary" />
              ) : (
                <Dropdown className="new-custom-dropdown">
                  <Dropdown.Toggle variant="" id="dropdown-basic">
                    <ThreeDotAction />
                  </Dropdown.Toggle>
                  <Dropdown.Menu>
                    {canCompleteReminder && (
                      <Dropdown.Item>
                        <div
                          className="d-flex align-items-center"
                          onClick={() =>
                            handleStatus(data.reminderId, "complete")
                          }
                        >
                          <TickIconCircle />{" "}
                          <span className="ms-1  dropdown-title">
                            Mark as Completed
                          </span>
                        </div>
                      </Dropdown.Item>
                    )}
                    {canEditReminder && (
                      <Dropdown.Item>
                        <div
                          className="d-flex align-items-center"
                          onClick={() => editReminder(data)}
                        >
                          <EditPenIconSquare />{" "}
                          <span className="ms-1 dropdown-title">
                            Edit Reminder
                          </span>
                        </div>
                      </Dropdown.Item>
                    )}
                    {canCancelReminder && (
                      <Dropdown.Item>
                        <div
                          className="d-flex align-items-center"
                          onClick={() => {
                            handleStatus(data.reminderId, "cancel");
                          }}
                        >
                          <CancelSquareIcon />{" "}
                          <span className="ms-1 dropdown-title">
                            Cancel Reminder
                          </span>
                        </div>
                      </Dropdown.Item>
                    )}
                  </Dropdown.Menu>
                </Dropdown>
              )}
            </div>
          </div>
        )}
      </div>
      <div className="heading-border mt-2"></div>
      <p className="data-label mt-3 reminder-desc">{data.description || ""}</p>

      <div className="reminder-bottom mt-2">
        <div className="d-flex align-items-center gap-2">
          <img src={datedesign} alt="" />
          <div className="fs-12-px mt-0 mb-0 text-grey2">
            {formatDate(data.selectDate, "DD MMM, YYYY")} | {data.selectTime}
          </div>
        </div>
      </div>
    </div>
  );
};

const InquiryReminder = ({ data, showAction }) => {
  const {
    editReminder,
    handleStatus,
    statusLoading,
    canCompleteReminder,
    canCancelReminder,
    canEditReminder,
    reminderId,
  } = useContext(ReminderContext);

  return (
    <div className="reminder-card">
      <div className="reminder-section">
        <div className="reminder-heading">
          <p className="mb-0">{data.title}</p>
        </div>
        {showAction && (
          <div className="">
            <div className="d-flex align-items-center justify-content-between mb-2">
              {statusLoading && reminderId === data.reminderId ? (
                <Spinner size="sm" variant="secondary" />
              ) : (
                <Dropdown className="new-custom-dropdown">
                  <Dropdown.Toggle variant="" id="dropdown-basic">
                    <ThreeDotAction />
                  </Dropdown.Toggle>
                  <Dropdown.Menu>
                    {canCompleteReminder && (
                      <Dropdown.Item>
                        <div
                          className="d-flex align-items-center"
                          onClick={() =>
                            handleStatus(data.reminderId, "complete")
                          }
                        >
                          <TickIconCircle />{" "}
                          <span className="ms-1  dropdown-title">
                            Mark as Completed
                          </span>
                        </div>
                      </Dropdown.Item>
                    )}
                    {canEditReminder && (
                      <Dropdown.Item>
                        <div
                          className="d-flex align-items-center"
                          onClick={() => editReminder(data)}
                        >
                          <EditPenIconSquare />{" "}
                          <span className="ms-1 dropdown-title">
                            Edit Reminder
                          </span>
                        </div>
                      </Dropdown.Item>
                    )}
                    {canCancelReminder && (
                      <Dropdown.Item>
                        <div
                          className="d-flex align-items-center"
                          onClick={() => {
                            handleStatus(data.reminderId, "cancel");
                          }}
                        >
                          <CancelSquareIcon />{" "}
                          <span className="ms-1 dropdown-title">
                            Cancel Reminder
                          </span>
                        </div>
                      </Dropdown.Item>
                    )}
                  </Dropdown.Menu>
                </Dropdown>
              )}
            </div>
          </div>
        )}
      </div>
      <div className="heading-border mt-2"></div>
      <h5 className="summary-title  mt-3">
        <span>E-ID: {data.inquiryInfoId}</span>
        <span className="ms-2 me-2">|</span>
        <span> {data.guestName}</span>
        <span className="ms-2 me-2">|</span>
        <span> {data.eventDescription}</span>
        {data.company && <span>-{data.company}</span>}
        {data.brideName && data.groomName && (
          <span>&nbsp;-&nbsp;{data.brideName + " & " + data.groomName}</span>
        )}
      </h5>
      <p className="data-label mt-3 reminder-desc">{data.description || ""}</p>
      <div className="reminder-bottom mt-2">
        <div className="d-flex align-items-center gap-2">
          <img src={datedesign} alt="" />
          <div className="fs-12-px mt-0 mb-0 text-purple">
            {formatDate(data.selectDate, "DD MMM, YYYY")} | {data.selectTime}
          </div>
        </div>
      </div>
    </div>
  );
};
