import { useContext, useMemo } from "react";
import { PermissionContext } from "../../../App";

const usePermission = (featureName, permissionName) => {
  const { userPermissions } = useContext(PermissionContext);
  const isAllowed = useMemo(() => {
    if (userPermissions) {
      const feature = userPermissions.find(
        (f) => f.featureName === featureName
      );
      if (feature) {
        const permission = feature.permissions.find(
          (p) => p.permissionName.trim() === permissionName.trim()
        );
        if (permission) {
          return permission.isAllowed;
        }
      }
    }
    return false;
  }, [userPermissions, featureName, permissionName]);

  return isAllowed;
};

export default usePermission;
