import Modal from "react-bootstrap/Modal";
import ConfirmImg from "../../../../../../assets/img/confirm-img.png";
import { Button } from "react-bootstrap";

const ConfirmAndNext = ({
  show,
  handleClose,
  onSubmitClick,
  onDiscardClick,
}) => {
  return (
    <Modal show={show} onHide={handleClose} className="custom-modal" centered>
      <Modal.Header closeButton>
        <Modal.Title>Confirm Action</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div>
          <div className="d-flex justify-content-center mb-3">
            <img src={ConfirmImg} alt="" />
          </div>
          <h4 style={{ fontSize: "20px" }} className="fw-bold text-center mb-4">
            Are you sure ?
          </h4>
          <p className="description mb-4 text-center ">
            Once you make changes, all the active inquiries will be updated
            accordingly
          </p>
        </div>
      </Modal.Body>
      <Modal.Footer>
        <div
          className="txt-primary fw-bolder me-3 fs-13-px"
          role="button"
          onClick={onDiscardClick}
        >
          Discard
        </div>
        <Button className=" fw-bolder fs-13-px" variant="primary" onClick={onSubmitClick}>
          Confirm
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default ConfirmAndNext;
