import { useContext, useEffect, useState } from "react";
import { PlusIcon } from "../../../../../../../../assets/img/svg";
import design from "../../../../../../../../assets/img/add-venue-details-design.png";
import TimeDetailsTable from "./TimeDetailsTable";
import { useParams } from "react-router-dom";
import settingsService from "../../../../../../../../services/api-services/settingsServices";
import apiStatus from "../../../../../../../../utils/apiResponseHandler";
import { toast } from "react-toastify";
import { Spinner } from "react-bootstrap";
import { enumList } from "../../../../../../../../utils/enumList";
import "./TimeDetails.scss";
import { VenueDetailsContext } from "../../VenueDetailsContext";

const TimeDetails = () => {
  const param = useParams();

  const [operationAction, setOperationAction] = useState(enumList.action.zero);
  const [isLoading, setIsLoading] = useState(true);
  const [venueTimeDetailsList, setVenueTimeDetailsList] = useState([]);

  useEffect(() => {
    if (param.venueId) {
      setIsLoading(true);
      getTimeDetailsById();
    } else {
      setOperationAction(enumList.action.zero);
    }
  }, []);

  const getTimeDetailsById = async () => {
    try {
      const response = await settingsService.getTimeDetailsById(param.venueId);
      const { detail, message, title, data = [] } = response;

      if (title === apiStatus.success) {
        let customData = data.filter(({ isDeleted }) => isDeleted !== true);
        if (customData.length === 0) {
          setVenueTimeDetailsList([]);
          setOperationAction(enumList.action.zero);
        } else {
          setVenueTimeDetailsList(customData);
          setOperationAction(enumList.action.read);
        }
      }
    } catch (error) {
      toast.error(error.message);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <>
      {isLoading ? (
        <div className="d-flex justify-content-center align-items-center h-100">
          <Spinner animation="border" className="txt-primary" />
        </div>
      ) : operationAction === enumList.action.zero ? (
        <AddTimeDetails setOperationAction={setOperationAction} />
      ) : (
        <TimeDetailsTable
          slotsData={venueTimeDetailsList}
          operationAction={operationAction}
          setOperationAction={setOperationAction}
          reload={getTimeDetailsById}
        />
      )}
    </>
  );
};

export default TimeDetails;

const AddTimeDetails = ({ setOperationAction, canAdd }) => {
  const { venueDetailsObject, permissions } = useContext(VenueDetailsContext);
  const { isVenueDeleted } = venueDetailsObject;

  return (
    <div className="h-100 d-flex align-items-center justify-content-center">
      <div className="text-center">
        <img height={180} width={180} src={design} alt="add-image" />
        <h3 className="page-heading mb-3">Add Time Details</h3>
        <p className="description-18 mb-4">
          Start adding time details as per your needs. You can modify them later
          too
        </p>
        {permissions?.canAdd && (
          <div className="d-flex justify-content-center">
            <button
              disabled={isVenueDeleted}
              className="btn btn-primary btn-34 px-3 add-btn"
              onClick={() => setOperationAction(enumList.action.edit)}
            >
              <PlusIcon />
              <span>Add New</span>
            </button>
          </div>
        )}
      </div>
    </div>
  );
};
