import React, { useState, useEffect } from "react";
import "./venue.scss";
import VenueDetails from "./VenueDetails";
import { VenueIconPurple } from "../../../../../../../assets/img/svg";

const VenueTab = ({ tabList, setActiveVenueId, activeVenueId }) => {
  const [venueData, setVenueData] = useState({});

  useEffect(() => {
    if (!activeVenueId) setActiveVenueId(tabList[0].id);
  }, [tabList]);

  useEffect(() => {
    const venue = tabList.find((item) => item.id === activeVenueId);
    setVenueData(venue);
  }, [activeVenueId, tabList]);

  return (
    <div className="venue-info-wrapper">
      <div className="venue-tabs">
        <ul className="nav nav-tabs custom-nav-tabs d-flex gap-3">
          {tabList?.map((item, index) => (
            <li
              className="nav-item"
              key={index}
              onClick={() => setActiveVenueId(item.id)}
              role="button"
            >
              <a
                className={`nav-link ${
                  item.id === activeVenueId ? "active" : ""
                }`}
              >
                <span className="me-1">
                  {item.id === activeVenueId && <VenueIconPurple />}
                </span>
                {item.venueName}
              </a>
            </li>
          ))}
        </ul>

        <VenueDetails venueInfoData={venueData} />
      </div>
    </div>
  );
};

export default VenueTab;
