import React from "react";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import img from "../../../../../assets/img/error-img.png";

const UserLimitReachedModal = ({ show, setShow }) => {
  const handleClose = () => setShow(false);
  return (
    <div className="custom-modal">
      <Modal show={show} onHide={handleClose} className="custom-modal" centered>
        <Modal.Header closeButton>
          <Modal.Title>Warning Action</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="inactive-account-modal-wrapper text-center">
            <img src={img} alt="" />
            <h4 className="fw-bold">Limit Reached</h4>
            <p className="description mb-3 text-center">
              You have reached maximum user limit.
              <br />
              Contact Boozno.com to increase users in your subscription.
            </p>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="primary" onClick={handleClose}>
            Okay
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default UserLimitReachedModal;
