import { Button, Modal } from "react-bootstrap";
import img from "../../../../assets/img/confirm-img.png";

const MarkCompleteReminderModal = ({ setShow, show, onConfirm }) => {
  const handleClose = () => {
    setShow(false);
  };
  return (
    <Modal show={show} onHide={handleClose} className="custom-modal" centered>
      <Modal.Header closeButton>
        <Modal.Title>Confirmation</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div className="text-center">
          <img src={img} alt="img" />
          <h4 className="fw-bold my-4">Mark as complete ?</h4>
          <p className="description mb-3 text-center">
            Once marked as Complete, the reminder cannot be activated or edited
            again
          </p>
        </div>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="primary" className="btn-34" onClick={onConfirm}>
          Confirm
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default MarkCompleteReminderModal;
