import React, { useEffect, useState } from "react";
import Offcanvas from "react-bootstrap/Offcanvas";
import AddUserConfirmationModal from "./popup-modals/AddUserConfirmationModal";
import CustomDropdown from "../../../common/custom-dropdown/CustomDropdown";
import { enumList } from "../../../../utils/enumList";
import {
  validateAlphabetString,
  validateEmail,
} from "../../../../utils/validationFunctions";
import settingsService from "../../../../services/api-services/settingsServices";
import apiStatus from "../../../../utils/apiResponseHandler";
import { Spinner } from "react-bootstrap";
import UserLimitReachedModal from "./popup-modals/UserLimitReachedModal";
import ConfirmChangesModal from "./popup-modals/ConfirmChangesModal";

const AddNewUserOffcanvas = ({
  show,
  setShow,
  userProfile,
  getUserData,
  getUsersList,
}) => {
  const [showConfirm, setConfirmModal] = useState(false);
  const [confirmUpdate, setConfirmUpdate] = useState(false);
  const [userDetails, setUserDetails] = useState({
    mobileCountryCode: enumList.countryCodes.text[1],
    titleId: enumList.titles.value.Mr,
    title: "",
    firstName: "",
    middleName: "",
    lastName: "",
    gender: "",
    genderId: null,
    mobileNumber: "",
    emailAddress: "",
    role: "",
    userRoleId: null,
  });
  const [errors, setErrors] = useState({});
  const [loading, setLoading] = useState(false);
  const [roles, setRoles] = useState([]);
  const [showLimitModal, setLimitModal] = useState(false);

  useEffect(() => {
    if (userProfile.id) {
      setUserDetails({ ...userProfile });
    }
  }, [userProfile]);

  useEffect(() => {
    if (show) {
      getUserRoles();
    }
  }, [show]);

  const handleChange = (e) => {
    const { value, name } = e.target;
    let object = { ...userDetails };
    object[name] = value;
    setUserDetails(object);
  };

  const validateForm = () => {
    let {
      firstName,
      lastName,
      emailAddress,
      mobileNumber,
      userRoleId,
      genderId,
      titleId,
    } = {
      ...userDetails,
    };
    let errorList = {};
    let valid = true;

    // first name validation
    if (firstName.length === 0) {
      errorList.firstName = "Please enter your first name";
      valid = false;
    } else if (!(firstName.length >= 1 && firstName.length <= 50)) {
      errorList.firstName = "Please enter valid first name";
      valid = false;
    } else if (!validateAlphabetString(firstName)) {
      errorList.firstName = "Please enter valid first name";
      valid = false;
    }

    // last name validation
    if (lastName.length === 0) {
      errorList.lastName = "Please enter last name";
      valid = false;
    } else if (!(lastName.length >= 1 && lastName.length <= 50)) {
      errorList.lastName = "Please enter valid last name";
      valid = false;
    } else if (!validateAlphabetString(lastName)) {
      errorList.lastName = "Please enter valid last name";
      valid = false;
    }

    // mobile number validation
    let mobileRegex = /^(0|91)?[6-9][0-9]{9}$/;

    if (mobileNumber.length === 0) {
      errorList.mobileNumber = "Please enter mobile number";
      valid = false;
    } else if (!mobileRegex.test(mobileNumber)) {
      errorList.mobileNumber = "Please enter a valid 10-digit mobile number";
      valid = false;
    }

    // email validation
    if (emailAddress.length === 0) {
      errorList.emailAddress = "Please enter email address";
      valid = false;
    } else if (!validateEmail(emailAddress)) {
      errorList.emailAddress = "Please enter valid email address";
      valid = false;
    }

    if (!titleId) {
      errorList.titleId = "Please select title";
      valid = false;
    }

    if (!genderId) {
      errorList.genderId = "Please select gender";
      valid = false;
    }

    if (!userRoleId) {
      errorList.userRoleId = "Please select role";
      valid = false;
    }

    setErrors(errorList);
    return valid;
  };

  const addEditUser = (e) => {
    e.preventDefault();
    if (validateForm()) {
      if (userDetails?.id) {
        setConfirmUpdate(true);
      } else {
        setConfirmModal(true);
      }
    }
  };

  const createUser = async () => {
    setLoading(true);
    let payload = {
      id: 0,
      titleId: userDetails.titleId,
      firstName: userDetails.firstName,
      middleName: userDetails.middleName,
      lastName: userDetails.lastName,
      genderId: userDetails.genderId,
      mobileCountryCode: enumList.countryCodes.text[1],
      mobileNumber: userDetails.mobileNumber,
      emailAddress: userDetails.emailAddress,
      userRoleId: userDetails.userRoleId,
      tenantId: "",
      userStatusId: 0,
      countryId: 0,
      stateId: 0,
      cityId: 0,
    };
    try {
      let response = await settingsService.addNewUser(payload);
      let { detail, title, data } = response;
      if (data && data === true) {
        setShow(false);
        getUsersList();
      } else if (detail.includes("limit")) {
        setLimitModal(true);
      } else {
        let apiErr = detail?.toLowerCase();
        if (apiErr?.includes("mobile")) {
          setErrors({ mobileNumber: detail });
        } else if (apiErr?.includes("email")) {
          setErrors({ emailAddress: detail });
        }
      }
    } catch (error) {
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  const updateUser = async () => {
    setLoading(true);
    let payload = { ...userDetails };
    try {
      let response = await settingsService.updateUserDetails(payload);
      let { detail, title } = response;
      if (title === apiStatus.success) {
        setShow(false);
        getUserData(userDetails.id);
      } else {
        let apiErr = detail?.toLowerCase();
        if (apiErr?.includes("mobile")) {
          setErrors({ mobileNumber: detail });
        } else if (apiErr?.includes("email")) {
          setErrors({ emailAddress: detail });
        }
      }
    } catch (error) {
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  const getUserRoles = async (id) => {
    try {
      setLoading(true);
      const response = await settingsService.getUserRolesList();
      const { title, data, detail } = response;
      if (detail === apiStatus.success) {
        let filtered = data
          .filter(
            (item) => item.id !== enumList.userRolesEnum.value.Administrator
          )
          .map((item) => ({ text: item.title, value: item.id }));
        setRoles(filtered);
      }
    } catch (error) {
      console.log(error.message);
    } finally {
      setLoading(false);
    }
  };

  const handleClose = () => setShow(false);

  return (
    <>
      <AddUserConfirmationModal
        setShow={setConfirmModal}
        show={showConfirm}
        onConfirm={createUser}
      />
      <ConfirmChangesModal
        show={confirmUpdate}
        setShow={setConfirmUpdate}
        onConfirm={updateUser}
      />
      <UserLimitReachedModal show={showLimitModal} setShow={setLimitModal} />

      <Offcanvas
        show={show}
        onHide={handleClose}
        placement={"end"}
        className="custom-modal"
      >
        <Offcanvas.Header closeButton>
          <Offcanvas.Title>Personal Info</Offcanvas.Title>
        </Offcanvas.Header>{" "}
        <Offcanvas.Body className="offcanvas-custom-body">
          <div className="h-100 d-flex flex-column justify-content-between p-3">
            <form onSubmit={addEditUser}>
              <div className="offcanvas-body-content p-3">
                <div className="offcanvas-form">
                  {userDetails.id && (
                    <div className="row">
                      <div className="col-md-12 mb-3">
                        <label
                          htmlFor=""
                          className="form-label custom-form-label"
                        >
                          Employee ID
                        </label>
                        <div className="fb-fullname-badge">
                          {userDetails.id}
                        </div>
                      </div>
                    </div>
                  )}
                  <div className="row">
                    <div className="col-md-3">
                      <div
                        className={`error-boundary ${
                          errors.titleId ? "error" : ""
                        }`}
                      >
                        <label className="form-label fs-14 mb-0">Title *</label>
                        <CustomDropdown
                          dataSource={enumList.titles.list}
                          value={userDetails.titleId}
                          name="titleId"
                          onChange={handleChange}
                        />

                        {errors.titleId && (
                          <div className="input-error-text">
                            {errors.titleId}
                          </div>
                        )}
                      </div>
                    </div>
                    <div className="col-md-9 mb-3">
                      <div
                        className={`error-boundary ${
                          errors.firstName ? "error" : ""
                        }`}
                      >
                        <label className="form-label fs-14 mb-0">
                          First Name *
                        </label>
                        <input
                          type="text"
                          className="form-control input-one-line"
                          placeholder="First Name"
                          name="firstName"
                          value={userDetails.firstName}
                          onChange={handleChange}
                        />

                        {errors.firstName && (
                          <div className="input-error-text">
                            {errors.firstName}
                          </div>
                        )}
                      </div>
                    </div>
                    <div className="col-md-12 mb-3">
                      <div
                        className={`error-boundary ${
                          errors.middleName ? "error" : ""
                        }`}
                      >
                        <label className="form-label fs-14 mb-0">
                          Middle Name
                        </label>
                        <input
                          type="text"
                          className="form-control input-one-line"
                          placeholder="Middle Name"
                          name="middleName"
                          value={userDetails.middleName}
                          onChange={handleChange}
                        />
                        {errors.middleName && (
                          <div className="input-error-text">
                            {errors.middleName}
                          </div>
                        )}
                      </div>
                    </div>
                    <div className="col-md-12 mb-3">
                      <div
                        className={`error-boundary ${
                          errors.lastName ? "error" : ""
                        }`}
                      >
                        <label className="form-label fs-14 mb-0">
                          Last Name *
                        </label>
                        <input
                          type="text"
                          className="form-control input-one-line"
                          placeholder="Last Name"
                          name="lastName"
                          value={userDetails.lastName}
                          onChange={handleChange}
                        />
                        {errors.lastName && (
                          <div className="input-error-text">
                            {errors.lastName}
                          </div>
                        )}
                      </div>
                    </div>
                    <div className="col-md-12 mb-3">
                      <div
                        className={`error-boundary ${
                          errors.genderId ? "error" : ""
                        }`}
                      >
                        <label className="form-label fs-14 mb-0">
                          Gender *
                        </label>
                        <CustomDropdown
                          dataSource={enumList.genderEnum.list}
                          value={userDetails.genderId}
                          name="genderId"
                          onChange={handleChange}
                          placeholder="Select gender"
                        />

                        {errors.genderId && (
                          <div className="input-error-text">
                            {errors.genderId}
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-md-12">
                      <div
                        className={`error-boundary ${
                          errors.mobileNumber ? "error" : ""
                        }`}
                      >
                        <label className="form-label">Mobile Number *</label>
                      </div>
                    </div>
                    <div className="col-md-3 col-sm-3 mb-3">
                      <CustomDropdown
                        // onChange={handleChange}
                        value={
                          enumList.countryCodes.value[
                            userDetails.mobileCountryCode
                          ]
                        }
                        name="mobileCountryCode"
                        dataSource={enumList.countryCodes.list}
                        placeholder="+91"
                        onChange={handleChange}
                      />
                    </div>
                    <div className="col-md-9 col-sm-9 mb-3">
                      <div
                        className={`error-boundary ${
                          errors.mobileNumber ? "error" : ""
                        }`}
                      >
                        <input
                          type="text"
                          className="form-control input-one-line"
                          placeholder="Enter Mobile Number"
                          onChange={handleChange}
                          name="mobileNumber"
                          value={userDetails.mobileNumber || ""}
                          maxLength={10}
                        />
                        {errors.mobileNumber && (
                          <div className="input-error-text">
                            {errors.mobileNumber}
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-md-12 mb-3">
                      <div
                        className={`error-boundary ${
                          errors.emailAddress ? "error" : ""
                        }`}
                      >
                        <label className="form-label fs-14 mb-0">
                          Email Address *
                        </label>
                        <input
                          type="email"
                          className="form-control input-one-line"
                          placeholder="Email Address"
                          name="emailAddress"
                          onChange={handleChange}
                          value={userDetails.emailAddress}
                        />
                        {errors.emailAddress && (
                          <div className="input-error-text">
                            {errors.emailAddress}
                          </div>
                        )}
                      </div>
                    </div>
                    <div className="col-md-12 mb-3">
                      <div
                        className={`error-boundary ${
                          errors.userRoleId ? "error" : ""
                        }`}
                      >
                        <label className="form-label fs-14 mb-0">Role *</label>
                        <CustomDropdown
                          dataSource={roles}
                          name="userRoleId"
                          placeholder="Select role"
                          value={userDetails.userRoleId}
                          onChange={handleChange}
                        />
                        {errors.userRoleId && (
                          <div className="input-error-text">
                            {errors.userRoleId}
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              <div className="offcanvas-footer-content">
                <div className="d-flex align-items-center justify-content-end gap-2">
                  <div
                    className="txt-primary fw-bold me-3 cursor-pointer"
                    onClick={() => setShow(false)}
                  >
                    Cancel
                  </div>
                  <button
                    className="btn btn-primary"
                    onClick={addEditUser}
                    type="submit"
                    disabled={loading}
                  >
                    {loading && (
                      <Spinner animation="border" size="sm" className="me-2" />
                    )}
                    {userDetails.id ? "Update" : "Add User"}
                  </button>
                </div>
              </div>
            </form>
          </div>
        </Offcanvas.Body>
      </Offcanvas>
    </>
  );
};

export default AddNewUserOffcanvas;
