import React, { useEffect, useState } from "react";
import uploaddesign from "../../../../assets/img/uploader.png";
import UserManagementHeader from "./UserManagementHeader";
import { renderUserStatus } from "../../../../utils/CommonUtils";
import apiStatus from "../../../../utils/apiResponseHandler";
import SpinnerLoader from "../../../common/spinner/SpinnerLoader";
import settingsService from "../../../../services/api-services/settingsServices";
import { toast } from "react-toastify";
import { enumList } from "../../../../utils/enumList";
import ActiveInquiryWarningModal from "./popup-modals/ActiveInquiryWarningModal";
import { useParams } from "react-router-dom";
import dayjs from "dayjs";
import male from "../../../../assets/img/male_image.png";
import female from "../../../../assets/img/female_image.png";

const UserProfile = () => {
  const [selectedImage, setSelectedImage] = useState(null);
  const [profileDetails, setProfileDetails] = useState({
    id: "",
    userSince: "-",
    userStatus: "-",
    firstName: "-",
    middleName: "-",
    lastName: "-",
    genderId: "-",
    genderName: "-",
    mobileNumber: "-",
    emailId: "-",
    userRoleId: "-",
    profileImage: null,
  });
  const [loading, setLoading] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [resendLoading, setResendLoading] = useState(false);

  const [activeInquiryShow, setActiveInquiryShow] = useState(false);
  const params = useParams();
  const { UserVerificationPending } = enumList.userAccountStatus.value;

  useEffect(() => {
    getUserData(params.userId);
  }, []);

  const handleImageChange = async (event) => {
    const file = event.target.files[0];
    const MAX_SIZE = 2 * 1024 * 1024;
    if (
      (file && file.type === "image/jpeg") ||
      file.type === "image/png" ||
      file.type === "image/jpg"
    ) {
      if (file.size > MAX_SIZE) {
        alert("File size exceeds the maximum limit (2MB)");
        return;
      }

      try {
        setIsLoading(true);
        const formData = new FormData();
        formData.append("File", file);

        const response = await settingsService.updateUserProfileImage(
          profileDetails.id,
          formData
        );
        const { title, detail } = response;
        if (title === apiStatus.success) {
          setProfileDetails({
            ...profileDetails,
            profileImage: `${detail}?timestamp=${dayjs().valueOf()}`,
          });
          event.target.value = "";
        }
      } catch (error) {
        console.error(error.message);
      } finally {
        setIsLoading(false);
      }
    } else {
      alert("Please upload a valid image file.");
    }
  };

  const resendVerificationLink = async () => {
    try {
      setResendLoading(true);
      const response = await settingsService.resendMemberVerifyLink(
        profileDetails
      );
      const { title, data, detail } = response;
      if (title === apiStatus.failure) {
        toast.error(detail, {
          position: "top-center",
        });
      } else {
        toast.success("Verification link has been resent.", {
          position: "top-center",
        });
        getUserData();
      }
    } catch (error) {
      toast.error(error.message);
    } finally {
      setResendLoading(false);
    }
  };

  const updateUserStatus = async (id, statusId, isDelete) => {
    try {
      setLoading(true);
      const response = await settingsService.updateUserStatus(id, statusId);
      const { title, detail } = response;
      if (title === apiStatus.success) {
        getUserData(params.userId);
      } else if (
        title === apiStatus.failure &&
        detail.includes("Active inquiries or booking found")
      ) {
        setActiveInquiryShow(true);
      }
    } catch (error) {
      toast.error(error.message);
    } finally {
      setLoading(false);
    }
  };

  const getUserData = async (id) => {
    try {
      setLoading(true);
      const response = await settingsService.getUsersDetails(id);
      const { title, data, detail } = response;
      if (detail === apiStatus.success) {
        setProfileDetails(data);
      }
    } catch (error) {
      toast.error(error.message);
    } finally {
      setLoading(false);
    }
  };

  const sentTime = dayjs(
    profileDetails?.updatedOn || profileDetails?.createdOn
  );
  const currentTime = dayjs();
  const hoursDifference = currentTime.diff(sentTime, "hour");

  return (
    <>
      <header className="header px-4">
        <UserManagementHeader
          userProfile={profileDetails}
          updateUserStatus={updateUserStatus}
          getUserData={getUserData}
        />
      </header>
      <div className="px-4 profile-container">
        {loading ? (
          <SpinnerLoader />
        ) : (
          <div className="row h-100">
            <div className="col-md-3">
              <div className="bg-custom-white h-100">
                <div className="profile-uploader">
                  <label htmlFor="imageUpload">
                    <div className="image-container position-relative ">
                      {isLoading ? (
                        <SpinnerLoader />
                      ) : (
                        <>
                          {!profileDetails.profileImage ? (
                            <img
                              src={
                                profileDetails.genderId === 1
                                  ? male
                                  : profileDetails.genderId === 2
                                  ? female
                                  : null
                              }
                              className="profile-image position-absolute"
                              alt="profiledesign"
                            />
                          ) : (
                            <img
                              src={profileDetails.profileImage}
                              className="profile-image position-absolute"
                              alt="profiledesign"
                            />
                          )}
                        </>
                      )}
                    </div>
                    <div className="profile-upload">
                      <img
                        src={uploaddesign}
                        alt="uploaddesign"
                        className="upload-img"
                      />
                    </div>
                  </label>
                </div>
                <input
                  type="file"
                  id="imageUpload"
                  accept="image/*"
                  style={{ display: "none" }}
                  onChange={handleImageChange}
                />
                <div className="d-flex gap-1 flex-column ">
                  <h4 className="title user-heading text-center mt-3">
                    {profileDetails.firstName + " " + profileDetails.lastName}
                  </h4>
                  <div className="d-flex justify-content-center">
                    <p className="design-desc text-center fw-bold mb-2">
                      {profileDetails.userRoleName}
                    </p>
                  </div>
                  {profileDetails.userRoleId ===
                    enumList.userRolesEnum.value.Administrator && (
                    <div className="d-flex justify-content-center ">
                      <p className="account-primary-badge">Primary</p>
                    </div>
                  )}
                </div>
              </div>
            </div>

            <div className="col-md-9">
              <div className="bg-custom-white h-100">
                <div className="d-flex align-items-center mb-4 gap-1">
                  <div>
                    <h4 className="title profile-heading mb-0">
                      Personal Info
                    </h4>
                  </div>
                  <div className="header-seperator"></div>
                </div>
                <div className="row custom-inputs">
                  <div className="col-md-3">
                    <div className="profile-data-wrapper">
                      <div className="data-header">User ID</div>
                      <div className="profile-value">{profileDetails.id}</div>
                    </div>
                  </div>
                  <div className="col-md-3">
                    <div className="profile-data-wrapper">
                      <div className="data-header">User Since</div>
                      <div className="profile-value">
                        {profileDetails.userSince}
                      </div>
                    </div>
                  </div>
                  <div className="col-md-3">
                    <div className="profile-data-wrapper">
                      <div className="data-header">Status</div>
                      <div className="profile-value d-flex ">
                        {hoursDifference > 24 &&
                        profileDetails.statusId === UserVerificationPending
                          ? "Link Expired."
                          : renderUserStatus(profileDetails.statusName)}
                        <>
                          {hoursDifference > 24 &&
                          profileDetails.statusId ===
                            UserVerificationPending ? (
                            <div
                              className="verification-link text-nowrap"
                              onClick={
                                !resendLoading ? resendVerificationLink : null
                              }
                            >
                              {resendLoading
                                ? "Sending..."
                                : "Resend Verification Link"}
                            </div>
                          ) : null}
                        </>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="heading-border mt-2"></div>
                <div className="row custom-inputs mt-4">
                  <div className="col-md-3">
                    <div className="profile-data-wrapper">
                      <div className="data-header">First Name </div>
                      <div className="profile-value">
                        {profileDetails.firstName}
                      </div>
                    </div>
                  </div>
                  <div className="col-md-3">
                    <div className="profile-data-wrapper">
                      <div className="data-header">Middle Name</div>
                      <div className="profile-value">
                        {profileDetails.middleName || "-"}
                      </div>
                    </div>
                  </div>
                  <div className="col-md-3">
                    <div className="profile-data-wrapper">
                      <div className="data-header">Last Name</div>
                      <div className="profile-value">
                        {profileDetails.lastName}
                      </div>
                    </div>
                  </div>
                  <div className="col-md-3">
                    <div className="profile-data-wrapper">
                      <div className="data-header">Gender</div>

                      <div className="profile-value">
                        {profileDetails.genderName}
                      </div>
                    </div>
                  </div>
                </div>
                <div className="row custom-inputs mt-4">
                  <div className="col-md-3">
                    <div className="profile-data-wrapper">
                      <div className="data-header">Mobile Number</div>

                      <div className="profile-value">
                        {profileDetails.mobileCountryCode +
                          " " +
                          profileDetails.mobileNumber}
                      </div>
                    </div>
                  </div>
                  <div className="col-md-3">
                    <div className="profile-data-wrapper">
                      <div className="data-header">Email Address</div>
                      <div
                        className="profile-value text-truncate"
                        title={profileDetails.emailAddress}
                      >
                        {profileDetails.emailAddress}
                      </div>
                    </div>
                  </div>
                  <div className="col-md-3">
                    <div className="profile-data-wrapper">
                      <div className="data-header">Access</div>
                      <div className="profile-value d-flex flex-row gap-2">
                        <div>{profileDetails.userRoleName}</div>
                        {profileDetails.userRoleId ===
                          enumList.userRolesEnum.value.Administrator && (
                          <p className="account-primary-badge">Primary</p>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
      <ActiveInquiryWarningModal
        show={activeInquiryShow}
        setShow={setActiveInquiryShow}
      />
    </>
  );
};

export default UserProfile;
