import { useEffect, useState } from "react";
import notificationServices from "../../../services/api-services/notificationServices";
import apiStatus from "../../../utils/apiResponseHandler";
import SpinnerLoader from "../../common/spinner/SpinnerLoader";
import { CalenderNotification, PersonIcon } from "../../../assets/img/svg";
import img from "../../../assets/img/man-img.png";
import notify from "../../../assets/img/new-notification.png";
import dayjs from "dayjs";
import { getTimeDifference } from "../../../utils/JSutils";
import NotificationCard from "./notification-cards/NotificationCard";

const TodaysNotification = () => {
  const [notifications, setNotifications] = useState([]);
  const [loading, setLoading] = useState([]);

  useEffect(() => {
    fetchNotifications();
  }, []);

  const fetchNotifications = async () => {
    try {
      setLoading(true);
      const response = await notificationServices.getTodaysNotification();
      const { detail, data, title } = response;
      if (title === apiStatus.success) {
        const updatedArr = data?.map((obj) => {
          const pairs = obj.notificationDescription.split(",");
          const notificationDescriptionObj = {};
          pairs.forEach((pair) => {
            const [key, value] = pair.split(/:(.+)/).map((item) => item.trim());
            notificationDescriptionObj[key] = value;
          });
          return {
            ...obj,
            desc: notificationDescriptionObj,
          };
        });
        setNotifications(updatedArr);
      }
    } catch (error) {
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="h-100">
      {loading ? (
        <SpinnerLoader />
      ) : notifications.length > 0 ? (
        notifications?.map((data, index) => (
          <NotificationCard key={index} data={data} />
        ))
      ) : (
        <div className="text-center h-100 d-flex justify-content-center align-items-center flex-column">
          <img src={notify} alt="notification" />
          <h5 className="guest-empty-notification">
            No new notifications as of now
          </h5>
        </div>
      )}
    </div>
  );
};

export default TodaysNotification;
