import axiosInstance from "../axiosInstance";
import { endPointsUrls } from "../endPointsUrls";

const notificationServices = {
  getTodaysNotification: () => {
    return axiosInstance.get(endPointsUrls.getTodaysNotification);
  },
  getWeeklyNotification: () => {
    return axiosInstance.get(endPointsUrls.getWeekNotification);
  },
  getMonthlyNotification: () => {
    return axiosInstance.get(endPointsUrls.getMonthNotification);
  },
  getNotificationCount: () => {
    return axiosInstance.get(endPointsUrls.getNotificationCountDetails);
  },
};

export default notificationServices;
