import {
  CalenderNotification,
  PersonIcon,
  VenueGray,
} from "../../../../assets/img/svg";
import img from "../../../../assets/img/man-img.png";
import { enumList } from "../../../../utils/enumList";
import { getTimeDifference } from "../../../../utils/JSutils";
import dayjs from "dayjs";
import { formatDate } from "../../../../utils/JSutils";

const NotificationCard = ({ data }) => {
  const {
    SuccessfulMemberVerification,
    DuplicateInquiryCreated,
    DuplicateInquiryDiscarded,
    Reminders,
    InquiryReassigned,
    InquiryModified,
    AnotherBookingConfirmedOverlapping,
    AlertsAnotherBookingConfirmedPrePost,
    AlertsAnotherBookingRequestSentWaitlist,
    AlertsAnotherBookingRequestApproved,
    AlertsNewInquiryAdded,
    DiscountsRequest,
    DiscountsRemoved,
    DiscountsRejected,
    DiscountsApproved,
    BookingRequest,
    BookingRequestCancelled,
    BookingRequestApproved,
    BookingRequestRejected,
    BookingApprovalCancelled,
    BookingConfirmed,
    BookingCancelled,
    BookingModified,
    PaymentsAdded,
    PaymentsModified,
    BookingMarkAsComplete,
    BookingCompleted,
    VenueCalendarUpdates,
    UserManagementDesignationUpdated,
    AccountDetailsVenueActiveInactive,
    AccountDetailsNewVenueAdded,
    AccountDetailsTaxUpdated,
    AccountPendingRenewal,
    AccountDetailsSubscriptionModified,
    AccountDetailsSubscriptionCancelled,
    AccountDetailsSubscriptionRenewed,
    AccountDetailsAccountOnHold,
    AccountDetailsSubscriptionSuspended,
    ImportImportStatus,
    UserDeleted,
    AccountDetailsVenueOverviewModifiedDeleted,
    AccountDetailsOperationalTimeModifiedDeleted,
    AccountDetailsVenueRentalsModifiedDeleted,
    AccountDetailsFoodBeveragePackageItemsModifiedDeleted,
    AccountDetailsFoodBeveragePackagesModifiedDeleted,
    AccountDetailsFoodBeverageMenuItemModifiedDeleted,
    AccountDetailsAmenitiesModifiedDeleted,
    AccountDetailsDocumentsModifiedDeleted,
    AccountDetailsAccountNearingRenewal7Days2Hrs,
  } = enumList.notificationType.value;

  if (
    [
      BookingRequest,
      BookingRequestCancelled,
      BookingRequestApproved,
      BookingRequestRejected,
    ].includes(data.notificationTypeId)
  ) {
    return (
      <div className="notification-item">
        <div className="notification-title-container">
          <Title title={data.title} />
          <RequestStatusTag data={data} />
        </div>

        <div className="border-bottom-notification mx-2" />
        <div className="notification-details-vertical-gap">
          <EventInfo info={data} />
          <div className="mb-2 details-container">
            <GuestName guestName={data.desc.Guest} />
            {(data.desc.RequestedBy ||
              data.desc.AcceptedBy ||
              data.desc.RejectedBy ||
              data.desc.CancelledBy) && (
              <div className="person-details">
                <div className="label">
                  {data.desc.RequestedBy && "Requested By"}
                  {data.desc.AcceptedBy && "Accepted By"}
                  {data.desc.RejectedBy && "Rejected By"}
                  {data.desc.CancelledBy && "Cancelled By"}
                </div>

                <div className="person-name">
                  <div className="d-flex align-items-center">
                    <img src={img} alt="" />
                    <span className="ms-1">
                      {data.desc.RequestedBy ||
                        data.desc.AcceptedBy ||
                        data.desc.RejectedBy ||
                        data.desc.CancelledBy}
                    </span>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
        <ReceivedTime eventDate={data.eventDate} />
      </div>
    );
  } else if (
    [
      DiscountsRequest,
      DiscountsRemoved,
      DiscountsRejected,
      DiscountsApproved,
    ].includes(data.notificationTypeId)
  ) {
    return (
      <div className="notification-item">
        <div className="notification-title-container">
          <Title title={data.title} />
          <div className="notification-status">
            {data.statusTag === "Accepted" && (
              <div className="accepted">Accepted</div>
            )}
            {data.statusTag === "Rejected" && (
              <div className="rejected">Rejected</div>
            )}
            {data.statusTag === "Removed" && (
              <div className="rejected">Removed</div>
            )}
          </div>
        </div>
        <div className="border-bottom-notification mx-2" />
        <div className="notification-details-vertical-gap">
          <EventInfo info={data} />
          <div className="mb-2 details-container">
            <GuestName guestName={data.desc.Guest} />
            {(data.desc.RequestedBy ||
              data.desc.AcceptedBy ||
              data.desc.RejectedBy ||
              data.desc.RemovedBy) && (
              <div className="person-details">
                <div className="label">
                  {data.desc.RequestedBy && "Requested By"}
                  {data.desc.AcceptedBy && "Accepted By"}
                  {data.desc.RejectedBy && "Rejected By"}
                  {data.desc.RemovedBy && "Removed By"}
                </div>

                <div className="person-name">
                  <div className="d-flex align-items-center">
                    <img src={img} alt="" />
                    <span className="ms-1">
                      {data.desc.RequestedBy ||
                        data.desc.AcceptedBy ||
                        data.desc.RejectedBy ||
                        data.desc.RemovedBy}
                    </span>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
        <ReceivedTime eventDate={data.eventDate} />
      </div>
    );
  } else if (
    [
      AccountDetailsVenueOverviewModifiedDeleted,
      AccountDetailsOperationalTimeModifiedDeleted,
      AccountDetailsVenueRentalsModifiedDeleted,
      AccountDetailsFoodBeveragePackageItemsModifiedDeleted,
      AccountDetailsFoodBeveragePackagesModifiedDeleted,
      AccountDetailsFoodBeverageMenuItemModifiedDeleted,
      AccountDetailsAmenitiesModifiedDeleted,
      AccountDetailsVenueActiveInactive,
      AccountDetailsNewVenueAdded,
    ].includes(data.notificationTypeId)
  ) {
    return (
      <div className="notification-item">
        <div className="notification-title-container">
          <Title title={data.title} />
        </div>
        <div className="border-bottom-notification mx-2" />
        <div className="notification-details-vertical-gap">
          {data.eventDate && <EventDate eventDate={data.eventDate} />}
          <div className="mb-2 details-container">
            <div className="person-details">
              <div className="label">Venue</div>
              <div className="icon-label">
                <VenueGray />
                <div className="ms-1 person-name">{data.desc.VenueName}</div>
              </div>
            </div>
            {data.desc.UpdatedBy && (
              <div className="person-details">
                <div className="label">
                  {data.desc.UpdatedBy && "Updated By"}
                </div>
                <div className="person-name">
                  <div className="d-flex align-items-center">
                    <img src={img} alt="" />
                    <span className="ms-1">{data.desc.UpdatedBy}</span>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
        <ReceivedTime eventDate={data.eventDate} />
      </div>
    );
  } else if (
    [
      BookingApprovalCancelled,
      BookingConfirmed,
      BookingCancelled,
      BookingModified,
      BookingMarkAsComplete,
      BookingCompleted,
    ].includes(data.notificationTypeId)
  ) {
    return (
      <div className="notification-item">
        <div className="notification-title-container">
          <Title title={data.title} />
          <div className="notification-status">
            {data.statusTag === "Completed" && (
              <div className="completed">Completed</div>
            )}
            {data.statusTag === "Cancelled" && (
              <div className="rejected">Cancelled</div>
            )}
          </div>
        </div>

        <div className="border-bottom-notification mx-2" />
        <div className="notification-details-vertical-gap">
          <EventInfo info={data} />

          {data.eventDate && <EventDate eventDate={data.eventDate} />}

          <div className="mb-2 details-container">
            <GuestName guestName={data.desc.Guest} />
            {(data.desc.UpdatedBy ||
              data.desc.CancelledBy ||
              data.desc.LeadOwner) && (
              <div className="person-details">
                <div className="label">
                  {data.desc.UpdatedBy && "Updated By"}
                  {data.desc.CancelledBy &&
                    data.statusTag === "Cancelled" &&
                    "Cancelled By"}
                  {data.desc.LeadOwner &&
                    data.statusTag === "Confirmed" &&
                    "LeadOwner"}
                </div>

                <div className="person-name">
                  <div className="d-flex align-items-center">
                    <img src={img} alt="" />
                    <span className="ms-1">
                      {data.desc.UpdatedBy ||
                        data.desc.CancelledBy ||
                        data.desc.LeadOwner}
                    </span>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
        <ReceivedTime eventDate={data.eventDate} />
      </div>
    );
  } else if (
    [
      AlertsAnotherBookingConfirmedPrePost,
      AlertsAnotherBookingRequestSentWaitlist,
      AlertsAnotherBookingRequestApproved,
      AlertsNewInquiryAdded,
      AnotherBookingConfirmedOverlapping,
      InquiryReassigned,
      InquiryModified,
    ].includes(data.notificationTypeId)
  ) {
    return (
      <div className="notification-item">
        <div className="notification-title-container">
          <Title title={data.title} />
        </div>
        <div className="border-bottom-notification mx-2" />
        <div className="notification-details-vertical-gap">
          <EventInfo info={data} />
          {data.eventDate && <EventDate eventDate={data.eventDate} />}

          <div className="mb-2 details-container">
            <GuestName guestName={data.desc.Guest} />
          </div>
        </div>
        <ReceivedTime eventDate={data.eventDate} />
      </div>
    );
  } else if ([Reminders].includes(data.notificationTypeId)) {
    return (
      <div className="notification-item">
        <div className="notification-title-container justify-content-start">
          <Title title={data.title} />
          {data.statusTag === "General Reminder" && (
            <div className="general-reminder ms-2">
              <div className="general">General Reminder</div>
            </div>
          )}
        </div>
        <div className="border-bottom-notification mx-2" />
        <div className="notification-title-container">
          <Title title={data.desc.ReminderTitle} />
        </div>

        <div className="notification-details-vertical-gap mt-0">
          <EventInfo info={data} />
          <div className="reminder-description mb-2">
            {data.desc.ReminderDescription}
          </div>
          <div className="notification-date mb-2">
            <div className="clender-icon">
              <CalenderNotification />
            </div>
            <div>
              {formatDate(data.desc.SelectDate.split(" ")[0], "DD MMM, YYYY")} |{" "}
              {data.desc.SelectTime}
            </div>
          </div>
        </div>
        <div className="notification-details-vertical-gap notification-last-time mb-2 mt-0">
          {getTimeDifference(data.eventDate)}
        </div>
      </div>
    );
  } else if (
    [DuplicateInquiryCreated, DuplicateInquiryDiscarded].includes(
      data.notificationTypeId
    )
  ) {
    return (
      <div className="notification-item">
        <div className="notification-title-container">
          <Title title={data.title} />
        </div>
        <div className="border-bottom-notification mx-2" />
        <div className="notification-details-vertical-gap duplicate-inquiry">
          <div className="duplicate-badge">Duplicate</div>
          <div className="notification-subtitle d-flex mb-2">
            <div className="p-0">E-ID: {data.desc["DE-ID"]}</div>
            {data.desc.DEvent && <div>{data.desc.DEvent}</div>}
            {data.desc?.DCompany && <div>{data.desc.DCompany}</div>}
            {data?.desc?.DBride && data?.desc?.DGroom && (
              <div>{data?.desc?.DBride + "&" + data?.desc?.DGroom}</div>
            )}
          </div>
          <EventDate eventDate={data.eventDate} />
          <div className="mb-2 details-container">
            <GuestName guestName={data.desc.DGuest} />
            <div className="person-details">
              <div className="label">Lead Owner</div>
              <div className="person-name">
                <div className="d-flex align-items-center">
                  <img src={img} alt="" />
                  <span className="ms-1">{data.desc.DLeadOwner}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="notification-details-vertical-gap duplicate-inquiry">
          <div className="original-badge">Original</div>
          <div className="notification-subtitle d-flex mb-2">
            <div className="p-0">E-ID: {data.desc["E-ID"]}</div>
            {data.desc.Event && <div>{data.desc.Event}</div>}
            {data.desc?.Company && <div>{data.desc.Company}</div>}
            {data?.desc?.Bride && data?.desc?.Groom && (
              <div>{data?.desc?.Bride + "&" + data?.desc?.Groom}</div>
            )}
          </div>
          <EventDate eventDate={data.eventDate} />
          <div className="mb-2 details-container">
            <GuestName guestName={data.desc.Guest} />
            <div className="person-details">
              <div className="label">Lead Owner</div>
              <div className="person-name">
                <div className="d-flex align-items-center">
                  <img src={img} alt="" />
                  <span className="ms-1">{data.desc.LeadOwner}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <ReceivedTime eventDate={data.eventDate} />
      </div>
    );
  } else if (
    [
      SuccessfulMemberVerification,
      UserDeleted,
      UserManagementDesignationUpdated,
    ].includes(data.notificationTypeId)
  ) {
    return (
      <div className="notification-item">
        <div className="notification-title-container">
          <Title title={data.title} />
        </div>
        <div className="border-bottom-notification mx-2" />
        <div className="notification-details-vertical-gap">
          <EventDate eventDate={data.eventDate} />
          <div className="mb-2 details-container">
            <div className="person-details">
              <div className="label">User</div>
              <div className="icon-label">
                <PersonIcon />
                <div className="ms-1 person-name">
                  {[data.desc.FirstName, data.desc.LastName].join(" ")}
                </div>
              </div>
            </div>

            <div className="person-details">
              <div className="label">Role</div>
              <div className="person-name">
                <div className="d-flex align-items-center">
                  <span>{data.desc.Role.trim()}</span>
                </div>
              </div>
            </div>

            {(data.desc.UpdatedBy ||
              data.desc.CancelledBy ||
              data.desc.LeadOwner) && (
              <div className="person-details">
                <div className="label">
                  {data.desc.UpdatedBy && "Updated By"}
                  {data.desc.CancelledBy &&
                    data.statusTag === "Cancelled" &&
                    "Cancelled By"}
                  {data.desc.LeadOwner &&
                    data.statusTag === "Confirmed" &&
                    "LeadOwner"}
                </div>

                <div className="person-name">
                  <div className="d-flex align-items-center">
                    <img src={img} alt="" />
                    <span className="ms-1">
                      {data.desc.UpdatedBy ||
                        data.desc.CancelledBy ||
                        data.desc.LeadOwner}
                    </span>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
        <ReceivedTime eventDate={data.eventDate} />
      </div>
    );
  } else if (
    [AccountDetailsDocumentsModifiedDeleted, AccountDetailsTaxUpdated].includes(
      data.notificationTypeId
    )
  ) {
    return (
      <div className="notification-item">
        <div className="notification-title-container">
          <Title title={data.title} />
        </div>
        <div className="border-bottom-notification mx-2" />
        <div className="notification-details-vertical-gap">
          <EventDate eventDate={data.eventDate} />
          <div className="mb-2 details-container">
            {(data.desc.UpdatedBy || data.desc.DeletedBy) && (
              <div className="person-details">
                <div className="label">
                  {data.desc.UpdatedBy && "Updated By"}
                  {data.desc.DeletedBy && "Deleted By"}
                </div>
                <div className="person-name">
                  <div className="d-flex align-items-center">
                    <img src={img} alt="" />
                    <span className="ms-1">
                      {data.desc.UpdatedBy || data.desc.DeletedBy}
                    </span>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
        <ReceivedTime eventDate={data.eventDate} />
      </div>
    );
  } else {
    return (
      <div className="notification-item">
        <div className="notification-title-container">
          <Title title={data.title} />
        </div>
        <div className="border-bottom-notification mx-2" />
        <div className="notification-details-vertical-gap">
          <EventInfo info={data} />
          <EventDate eventDate={data.eventDate} />
        </div>
        <ReceivedTime eventDate={data.eventDate} />
      </div>
    );
  }
};

export default NotificationCard;

const Title = ({ title }) => {
  return <b className=" mb-2 notification-title">{title}</b>;
};

const ReceivedTime = ({ eventDate }) => {
  return (
    <div className="notification-details-vertical-gap notification-last-time mb-2">
      {getTimeDifference(eventDate)}
    </div>
  );
};

const GuestName = ({ guestName }) => {
  return (
    <div className="person-details">
      <div className="label">Guest</div>
      <div className="icon-label">
        <PersonIcon />
        <div className="ms-1 person-name">{guestName}</div>
      </div>
    </div>
  );
};

const EventDate = ({ eventDate }) => {
  return (
    <div className="notification-date mb-2">
      <div className="clender-icon">
        <CalenderNotification />
      </div>
      <div>{dayjs(eventDate).format("DD MMM, YYYY")}</div>
    </div>
  );
};

const EventInfo = ({ info }) => {
  return (
    <div className="notification-subtitle d-flex mb-2">
      {info.desc["E-ID"] && <div>E-ID : {info.desc["E-ID"]}</div>}
      <div>{info.desc.Event}</div>
      {info.desc.Company && <div>{info.desc.Company}</div>}
      {info?.desc?.Bride && (
        <div>{info?.desc?.Bride + "&" + info?.desc?.Groom}</div>
      )}
    </div>
  );
};

const RequestStatusTag = ({ data }) => {
  return (
    <div className="notification-status">
      {data.statusTag === "Accepted" && (
        <div className="accepted">Accepted</div>
      )}
      {data.statusTag === "Rejected" && (
        <div className="rejected">Rejected</div>
      )}
      {data.statusTag === "Cancelled" && (
        <div className="rejected">Cancelled</div>
      )}
    </div>
  );
};
