import axiosInstance from "../axiosInstance";
import { endPointsUrls } from "../endPointsUrls";

const remindersServices = {
  addGeneralReminder: (payload) => {
    return axiosInstance.post(endPointsUrls.addGeneralReminder, payload);
  },
  getAllReminderDetails: () => {
    return axiosInstance.get(endPointsUrls.getAllReminderDetailsList);
  },
  updateReminderStatus: (id, statusId) => {
    return axiosInstance.post(
      endPointsUrls.updateReminderStatus +
        `?ReminderId=${id}&StatusId=${statusId}`
    );
  },
};

export default remindersServices;
