import React, { useState } from "react";
import {
  ActionToggle,
  EditIconSmall,
  PlusIcon,
  TickCircleIconSmall,
  TrashIconSmall,
} from "../../../../assets/img/svg";
import AddNewUserOffcanvas from "./AddNewUserOffCanvas";
import { enumList } from "../../../../utils/enumList";
import DeleteUserModal from "./popup-modals/DeleteUserModal";
import { useLocation } from "react-router-dom";

const UserManagementHeader = ({
  getUsersList,
  userProfile,
  updateUserStatus,
  getUserData,
}) => {
  const [userOffcanvasShow, setUserOffcanvasShow] = useState(false);
  const { Active, InActive, LinkExpired, UserVerificationPending, Deleted } =
    enumList.userAccountStatus.value;
  const [deleteUserShow, setDeleteUserShow] = useState(false);

  const handleDeleteClick = () => {
    let isDelete = true;
    updateUserStatus(userProfile.id, Deleted, isDelete);
  };

  const handleUserStatus = () => {
    let status = userProfile.statusId === Active ? InActive : Active;
    updateUserStatus(userProfile.id, status);
  };

  const { search } = useLocation();

  return (
    <>
      <div className="w-100 d-flex align-items-center justify-content-between">
        {!userProfile?.id ? (
          <>
            <h4 className="heading-title mb-0">User Management</h4>
            {search === '?tab=users' && (
              <button
                className="btn btn-primary d-flex align-items-center"
                onClick={() => setUserOffcanvasShow(true)}
              >
                <span className="mb-1 me-2">
                  <PlusIcon />
                </span>
                <span className="fs-14">Add New</span>
              </button>
            )}
          </>
        ) : (
          <>
            <div className="name-wrapper txt-primary d-flex align-items-center justify-content-center">
              <div className="user-title">
                {userProfile.firstName + " " + userProfile.lastName || "-"}
              </div>
            </div>
            {userProfile.statusId !== Deleted &&
              userProfile.userRoleId !==
                enumList.userRolesEnum.value.Administrator && (
                <div className="btn-group">
                  <button
                    type="button"
                    className="btn btn-danger canvasbtn d-flex align-items-center"
                  >
                    <span className="">Action</span>
                  </button>
                  <button
                    type="button"
                    className="btn btn-danger canvasbtn-toggle dropdown-toggle dropdown-toggle-split"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                  >
                    <span className="toggle-hidden">
                      <ActionToggle strokeColor={"#FFFFFF"} />
                    </span>
                  </button>
                  <ul className="dropdown-menu py-2 px-3">
                    {userProfile.statusId !== LinkExpired &&
                      userProfile.statusId !== UserVerificationPending && (
                        <li>
                          <div
                            className="d-flex align-items-center mb-1 cursor-pointer"
                            onClick={handleUserStatus}
                          >
                            <TickCircleIconSmall />
                            <span className="dropdown-title text-nowrap ">
                              {userProfile.statusId === Active
                                ? "Mark as Inactive"
                                : "Mark as Active"}
                            </span>
                          </div>
                        </li>
                      )}

                    <li>
                      <div
                        className="d-flex align-items-center mb-1 cursor-pointer"
                        onClick={() => setUserOffcanvasShow(true)}
                      >
                        <EditIconSmall />
                        <span className="dropdown-title">Edit Details</span>
                      </div>
                    </li>
                    <li>
                      <div
                        className="d-flex align-items-center cursor-pointer"
                        onClick={() => setDeleteUserShow(true)}
                      >
                        <TrashIconSmall />
                        <span className="dropdown-title">Delete User</span>
                      </div>
                    </li>
                  </ul>
                </div>
              )}
          </>
        )}
      </div>
        <AddNewUserOffcanvas
          show={userOffcanvasShow}
          setShow={setUserOffcanvasShow}
          userProfile={userProfile?.id ? userProfile : {}}
          getUserData={getUserData}
          getUsersList={getUsersList}
        />
        <DeleteUserModal
          show={deleteUserShow}
          setShow={setDeleteUserShow}
          onConfirm={handleDeleteClick}
        />
    </>
  );
};

export default UserManagementHeader;
